
// choose
.choose {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__card {
        &_top {
            padding-bottom: 40px;
            border-bottom: 2px solid var(--temnyy-10);
            margin-bottom: 40px;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: 16px;
                height: 16px;
                border-radius: 100%;
                background: var(--belyy-100);
                border: 5px solid var(--osnovnoy-100);
                outline: 8px solid var(--belyy-100);
                position: absolute;
                bottom: -8px;
                left: 0;
            }
        }

        &_icon {
            display: block;
            width: 38px;
            aspect-ratio: 1 / 1;
            margin-bottom: 33px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
        }

        &_text {
            color: var(--temnyy-60);
            & p {
                max-height: 102px;
                overflow: hidden;
                margin-bottom: 16px;
            }
        }

        &_btn-more {
            border-radius: 10px;
            padding: 10px 16px;
            background: var(--seryy);
            font-weight: 500;
            font-size: 16px;
            color: var(--temnyy-100);
        }
    }
}

// media choose
@media (max-width: 768px) {
    .choose {
        &__card {
            &_top {
                padding-bottom: 24px;
                margin-bottom: 24px;
                position: relative;
    
                &::before {
                    width: 14px;
                    height: 14px;
                    outline: 6px solid var(--belyy-100);
                    bottom: -7px;
                }
            }
            &_title {
                font-size: 18px;
            }
    
            &_text {
                & p {
                    max-height: 88px;
                }
            }
    
            &_btn-more {
                padding: 10px 14px;
                font-size: 14px;
            }
        }
        &__swiper-btns {
            display: none;
        }
    }
}