
// contacts
.contacts {
    &__title {
        display: flex;
        justify-content: space-between;
        gap: 40px;
        margin-bottom: 32px;
    }

    &__items {
        display: flex;
        gap: 20px;
    }

    &__item {
        flex: 1 0 30%;
        border-radius: 10px;
        padding: 24px;
        background: var(--seryy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 42px;

        &_top {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            color: var(--temnyy-50-obvodka);
        }

        &_text {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 20px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            text-align: left;
            transition: all 0.2s ease-in;
            &:hover {
                color: var(--osnovnoy-100);
            }
        }

        &_icon {
            display: flex;
            align-items: center;
            gap: 8px;
            &::after {
                content: ")";
            }
            &::before {
                content: "(";
            }
            &::after, &::before {
                display: block;
                color: var(--temnyy-50-obvodka);
                font-size: 22px;
            }
        }
    }

    &__maps {
        border-radius: 10px;
        width: 100%;
        height: 100%;
        min-height: 690px;
        position: relative;
        overflow: hidden;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }

}

.contacts-page {
    & .contacts__items {
        width: 100%;
    }
}

// media contacts
@media (max-width: 1280px) {
    .contacts {
        &__title {
            flex-direction: column;
            gap: 30px;
            margin-bottom: 40px;
        }
    }
}
@media (max-width: 1000px) {
    .contacts {
        &__items {
            flex-direction: column;
            gap: 12px;
        }
    }
}
@media (max-width: 768px) {
    .contacts {
        overflow: hidden;
        margin-bottom: 0;
        &__item {
            flex: 1 0 30%;
            padding: 20px;
            gap: 24px;
            &_text {
                font-size: 18px;
            }
            &_icon {
                & img {
                    max-height: 16px;
                    object-fit: contain;
                }
                &::after, &::before {
                    font-size: 18px;
                }
            }
        }
    
        &__maps {
            border: 0;
            min-height: 380px;
            margin: 0 -10px;
            width: calc(100% + 20px);
        }
    }
}