
// how-going
.how-going {
    overflow: hidden;

    &__time {
        border-radius: 10px;
        padding: 24px;
        background: var(--osnovnoy-100);
        display: flex;
        gap: 20px;

        &_item {
            flex: 1 0 auto;
            &:first-child {
                padding-right: 20px;
                border-right: 1px solid var(--belyy-20-obvodka-hover);
            }

            & p {
                font-size: 16px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--belyy-70);
                margin-bottom: 12px;
            }

            & span {
                font-weight: 500;
                font-size: 24px;
                line-height: 90%;
                letter-spacing: -0.02em;
                color: var(--belyy-100);
            }
        }
    }

    &__swiper {
        overflow: visible;
        padding-bottom: 14px;
    }

    & .swiper-wrapper {
        list-style-type: none;
        counter-reset: item;
    }

    &__card {
        border-radius: 10px;
        padding: 32px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.06);
        background: var(--belyy-100);
        color: var(--temnyy-60);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        min-height: 400px;

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);

            &::before {
                content: counter(item, decimal-leading-zero);
                counter-increment: item;
                display: block;
                border-radius: 10px;
                padding: 8px 12px;
                background: var(--osnovnoy-5);
                font-weight: 500;
                font-size: 20px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--osnovnoy-100);
                width: fit-content;
                margin-bottom: 20px;
            }
        }
    }
}

// media how-going
@media (max-width: 940px) {
    .how-going {
        &__title {
            flex-direction: column;
            align-items: stretch;
            & .title-h2 {
                max-width: 100%;
            }
        }
        &__time {
            &_item {
                text-align: center;
            }
        }
    }
}
@media (max-width: 768px) {
    .how-going {
        &__time {
            padding: 20px;
            gap: 16px;
            &_item {
                text-align: left;
                &:first-child {
                    padding-right: 16px;
                }
                & p {
                    font-size: 14px;
                    margin-bottom: 4px;
                }
                & span {
                    font-size: 22px;
                }
            }
        }

        &__card {
            padding: 20px;
            min-height: 240px;
    
            &_title {
                font-size: 20px;
                display: flex;
                align-items: center;
                gap: 16px;
    
                &::before {
                    padding: 16px;
                    font-size: 18px;
                    margin-bottom: 0;
                }
            }
        }
    }
}