
// steps
.steps {
    padding-top: 100px;
    overflow: hidden;

    & .swiper-wrapper {
        list-style-type: none;
        counter-reset: item;
    }

    &__swiper-slide {
        height: auto;

        &:nth-child(even) {
            & .steps__card {
                margin-top: 80px;
            }
        }
    }

    &__card {
        border-radius: 10px;
        padding: 28px;
        background: var(--belyy-100);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        min-height: 440px;
        position: relative;

        &_icon {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 10px;
            background: var(--osnovnoy-5);
            width: 64px;
            aspect-ratio: 1/1;
        }

        &::before {
            content: counter(item);
            counter-increment: item;
            font-family: var(--second-family);
            font-weight: 500;
            font-size: 110px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--seryy);
            position: absolute;
            top: 28px;
            right: 28px;
        }
    }

}

// media steps
@media (max-width: 1300px) {
    .steps {
        & .title-h2 {
            max-width: 100%;
        }
        &__swiper {
            overflow: visible;
        }
    }
}

@media (max-width: 768px) {
    .steps {
        padding-top: 40px;

        &__swiper-slide {
            &:nth-child(even) {
                & .steps__card {
                    margin-top: 0;
                }
            }
        }
    
        &__card {
            padding: 20px;
            min-height: 260px;
    
            &_icon {
                width: 56px;
            }
    
            &::before {
                top: 20px;
                right: 20px;
            }
        }
    
    }
}