
// linking
.linking {
    &__flex {
        display: flex;
        align-items: flex-start;
        gap: 40px;
    }

    &__sort {
        width: 100%;
        max-width: 395px;
        border-radius: 10px;
        padding: 32px;
        background: var(--seryy);

        &_text {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 40px;
        }
    }

    &__block {
        &:not(:last-child) {
            padding-bottom: 40px;
            border-bottom: 1px solid var(--temnyy-10);
            margin-bottom: 40px;
        }
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 48px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--temnyy-100);
        margin-bottom: 32px;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 12px 20px;

        & a {
            border-radius: 10px;
            padding: 12px 12px 12px 24px;
            background: var(--seryy);
            font-weight: 500;
            line-height: 140%;
            letter-spacing: -0.02em;
            color: var(--temnyy-100);

            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 12px;
            width: 100%;
            height: 100%;

            &::after {
                content: "";
                display: block;
                min-width: 60px;
                width: 60px;
                height: 60px;
                background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='60' height='60' rx='6' fill='white'/%3E%3Cpath d='M22 30H38M38 30L30.8889 37M38 30L30.8889 23' stroke='%23F71729' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-size: contain;
                transition: all 0.2s ease-in;
            }

            &:hover::after  {
                background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='60' height='60' rx='6' fill='%23F71729'/%3E%3Cpath d='M22 30H38M38 30L30.8889 37M38 30L30.8889 23' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }

    &__accordion, &__btn-more, & .accordion__content {
        display: contents;
    }
}

// media linking
@media (max-width: 1300px) {
    .linking {
        &__items {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media (max-width: 1024px) {
    .linking {
        &__flex {
            display: block;
        }
        &__sort {
            display: none;
        }
    }
}

@media (max-width: 768px) {
    .linking {

        &__block {
            &:not(:last-child) {
                padding-bottom: 24px;
                margin-bottom: 24px;
            }
        }

        &__title {
            font-size: 24px;
            margin-bottom: 0;
        }

        &__accordion {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;

            &.accordion--active {
                margin-bottom: 24px;
                & .linking__btn-more::after {
                    transform: rotate(225deg);
                }
            }
        }

        &__btn-more {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            min-width: 32px;
            width: 32px;
            height: 32px;
            background: var(--osnovnoy-5);

            &::after {
                content: "";
                display: inline-block;
                width: 8px;
                height: 8px;
                border-bottom: 2px solid var(--osnovnoy-100);
                border-right: 2px solid var(--osnovnoy-100);
                transform: rotate(45deg);
                transition: all 0.3s ease;
            }
        }

        &__items {
            grid-template-columns: repeat(1, 1fr);
            gap: 12px;

            & a {
                padding: 12px 12px 12px 20px;
                &::after {
                    min-width: 48px;
                    width: 48px;
                    height: 48px;
                }
            }
        }

        & .accordion__content {
            display: block;
        }
    }
}