// error
.error {
    padding: 200px 0;
    &__inner {
        max-width: 1004px;
        margin: 0 auto;
    }

    &__picture {
        display: block;
        margin: 0 auto;
        width: fit-content;
        margin-bottom: 40px;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 60px;
        line-height: 105%;
        letter-spacing: -0.01em;
        text-align: center;
        color: var(--temnyy-100);
        margin-bottom: 32px;
    }

    &__btn {
        display: block;
        margin: 0 auto;
        margin-bottom: 60px;
        width: fit-content;
    }

    &__text {
        text-align: center;
        color: var(--temnyy-60);
        margin-bottom: 30px;
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 12px 20px;
    }

    &__link {
        width: calc(50% - 10px);
        border-radius: 10px;
        padding: 12px 12px 12px 24px;
        background: var(--seryy);
        font-weight: 500;
        color: var(--temnyy-100);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;

        &::after {
            content: "";
            display: block;
            min-width: 60px;
            width: 60px;
            height: 60px;
            background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='60' height='60' rx='6' fill='white'/%3E%3Cpath d='M22 30H38M38 30L30.8889 37M38 30L30.8889 23' stroke='%23F71729' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E");
            background-repeat: no-repeat;
            background-size: contain;
            transition: all 0.2s ease-in;
        }

        &:hover::after  {
            background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='60' height='60' rx='6' fill='%23F71729'/%3E%3Cpath d='M22 30H38M38 30L30.8889 37M38 30L30.8889 23' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E");
            background-repeat: no-repeat;
            background-size: contain;
        }
    }
}

// media error
@media (max-width: 688px) {
    .error {
        padding: 80px 0;
        &__inner {
            max-width: 1004px;
            margin: 0 auto;
        }

        &__picture {
            margin-bottom: 30px;
        }

        &__title {
            font-size: 26px;
            margin-bottom: 16px;
        }

        &__btn {
            margin-bottom: 40px;
            width: 100%;
        }

        &__text {
            margin-bottom: 16px;
        }

        &__link {
            width: 100%;
            &::after {
                min-width: 48px;
                width: 48px;
                height: 48px;
            }
        }
    }
}