.popup-change {
    &__content {
        max-width: 640px;
        border-radius: 10px;
        padding: 80px 60px;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 48px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--temnyy-100);
        margin-bottom: 16px;
    }

    &__subtitle {
        color: var(--temnyy-60);
        max-width: 78%;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__btn {
        width: 100%;
    }

    &__close {
        padding: 14px;
        background: var(--osnovnoy-5);
        border: 1px solid transparent;

        &:hover {
            background: transparent;
            border: 1px solid var(--osnovnoy-100);
        }
    }
}




.popup-change-response {
    &__content {
        max-width: 500px;
        padding: 60px;
        display: flex;
        flex-direction: column;
    }
    &__wrapper {
        & h3 {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            text-align: center;
            color: var(--temnyy-100);
            margin-bottom: 12px;


            display: flex;
            flex-direction: column;
            gap: 20px;
            text-align: center;
            &::before {
                content: url("./../img/icons/popup-ok.svg");
            }
        }

        & p {
            color: var(--temnyy-60);
            text-align: center;
        }
    }
    &__btn {
        margin: 0 auto;
        margin-top: 32px;
        
        & button {
            margin: 0 auto;
        }
    }
}

.popup[data-target="popup-change-ok"] {
    & h3::before {
        content: url("./../img/icons/popup-ok.svg");
    }
}
.popup[data-target="popup-change-error"] {
    & h3::before {
        content: url("./../img/icons/popup-error.svg");
    }
}



// media popup-change
@media (max-width: 768px) {
.popup-change {
    &__content {
        padding: 60px 20px;
    }

    &__title {
        font-size: 26px;
    }

    &__subtitle {
        max-width: 100%;
    }

    &__close {
        padding: 11px;
    }
}

    .popup-change-response {
        &__content {
            max-width: 400px;
            padding: 10px;
            display: flex;
            flex-direction: column;
        }
        &__wrapper {
            & h3 {
                border-radius: 5px;
                padding: 16px 10px;
                font-size: 30px;
            }
            & p {
                gap: 14px;
                padding: 0 10px 20px;
            }
        }
        &__btn {
            padding-top: 20px;
            margin: 0 20px 20px;
        }
    }
}
