/* submenu-3 */
.submenu-3 {
    position: absolute;
    top: calc(100% + 45px);
    left: -32px;
    border-radius: 0 0 10px 10px;
    padding: 22px;
    background: var(--belyy-100);
    min-width: 300px;

    & ul {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        gap: 0px;

        & li {
            & a {
                display: block;
                padding: 10px 0;
                font-weight: 500;
                font-size: 18px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--temnyy-100);
                width: 100%;
                transition: all 0.3s ease;

                &:hover {
                    color: var(--osnovnoy-100);
                }
            }
        }
    }
}

// media submenu-3
@media (max-width: 1460px) {
    .submenu-3 .container {
        display: contents;
    }
    .submenu-3 {
        position: static;
        padding: 0 16px;
        transform: translateX(0);
        -webkit-box-ordinal-group: 4;
            -ms-flex-order: 3;
                order: 3;
        min-width: 100%;
        margin-top: 10px;
        & ul {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            & li {
                & a {
                    padding: 5px 0;
                    font-size: 16px;
                    width: 100%;
                    max-width: 100%;
                    min-height: auto;
                    -webkit-box-orient: horizontal;
                    -webkit-box-direction: normal;
                        -ms-flex-direction: row;
                            flex-direction: row;
                    -webkit-box-align: center;
                        -ms-flex-align: center;
                            align-items: center;
                }
            }
        }
    }
}
