
// reviews
.reviews {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__card {
        border-radius: 10px;
        padding: 40px;
        background: var(--seryy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_fio {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            margin-bottom: 24px;

            &::before {
                content: "";
                display: block;
                width: 30px;
                height: 19px;
                background: url("./../img/icons/quote.svg") no-repeat center / contain;
                margin-bottom: 12px;
            }
        }

        &_text {
            color: var(--temnyy-60);
            margin-bottom: 32px;
            & p {
                max-height: 122px;
                overflow: hidden;
                margin-bottom: 16px;
            }
        }

        &_btn-more {
            border-radius: 10px;
            padding: 10px 16px;
            background: var(--belyy-100);
            font-weight: 500;
            font-size: 16px;
            color: var(--temnyy-100);
        }

        &_items {
            display: flex;
            flex-wrap: wrap;
            gap: 8px;
            margin: 0 -28px -28px -28px;
        }

        &_item {
            flex: 1 0 40%;
            border-radius: 5px;
            padding: 16px;
            background: var(--belyy-100);
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            letter-spacing: -0.02em;
            color: var(--temnyy-50-obvodka);
            display: flex;
            justify-content: space-between;
            gap: 12px;

            & > * {
                flex: 1 0 40%;
            }

            & span {
                color: var(--temnyy-100);
                text-align: right;
            }

            & a {
                text-decoration: underline;
                color: var(--osnovnoy-100);
            }
        }
    }



    &__screen {
        border-radius: 10px;
        overflow: hidden;
        position: relative;

        &_picture {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                max-height: 450px;
                object-fit: cover;
                object-position: top;
            }
        }

        &_label {
            border: 1px solid var(--temnyy-10);
            border-radius: 10px;
            padding: 16px 24px;
            background: var(--belyy-100);
            font-weight: 500;
            text-align: center;
            color: var(--temnyy-100);
            width: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__swiper-scrollbar {
        margin-bottom: 36px;
    }
}

.reviews__grid-text {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

// media reviews
@media (max-width: 1200px) {
    .reviews__grid-text {
        grid-template-columns: repeat(1, 1fr);
        gap: 20px;
    }
}
@media (max-width: 940px) {
    .reviews {
        &__card_item {
                flex: 1 0 100%;
        }
    }
}
@media (max-width: 768px) {
    .reviews {
        overflow: hidden;

        &__title {
            flex-direction: column;
            align-items: stretch;
        }

        &__tab-btns > button {
            flex-grow: 1;
        }

        &__card {
            padding: 20px 24px;
    
            &_fio {
                font-size: 20px;
                margin-bottom: 20px;
                &::before {
                    width: 24px;
                    height: 15px;
                }
            }
    
            &_text {
                margin-bottom: 28px;
                & p {
                    max-height: 180px;
                }
            }
    
            &_btn-more {
                padding: 11px 14px;
                font-size: 14px;
            }
    
            &_items {
                gap: 6px;
                margin: 0 -18px -12px -18px;
            }
    
            &_item {
                padding: 12px;
                gap: 20px;
                & > * {
                    flex: 0 1 auto;
                }
            }
        }
    
        &__screen {
            &_picture {
                & img {
                    max-height: 320px;
                }
            }
            &_label {
                padding: 14px 20px;
            }
        }
    
        &__bottom {
            display: contents;
        }
    
        &__swiper-scrollbar, &__swiper-btns {
            display: none;
        }
        &__swiper-pagination {
            margin-bottom: 20px;
        }
    }
}