// intro
.intro {
    padding-top: 64px;

    &__row {
        display: flex;
        gap: 20px;
    }

    &__text {
        max-width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 24px;
    }

    &__price {
        border: 1px solid var(--osnovnoy-20);
        border-radius: 10px;
        padding: 12px 20px;
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--osnovnoy-100);
        width: fit-content;
        margin-bottom: 24px;
    }

    &__title {
        margin-bottom: 24px;
    }

    &__subtitle {
        margin-bottom: 24px;
        max-width: 64%;
    }

    &__btn {
        width: fit-content;
    }

    &__picture {
        max-width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        & img {
            width: 100%;
            object-fit: cover;
        }
    }
}


// media intro
@media (max-width: 1200px) {
    .intro {
        &__row {
            flex-direction: column-reverse;
            gap: 24px;
        }
        &__text {
            max-width: 100%;
        }
        &__text_top {
            display: flex;
            flex-direction: column;
        }
        &__picture {
            margin: 0 auto;
        }
        &__subtitle {
            margin-bottom: 30px;
        }
        &__price {
            border: none;
            order: 3;
            padding: 0;
            font-weight: 400;
            font-size: 14px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--temnyy-60);
            width: 100%;
            display: flex;
            justify-content: space-between;
            gap: 10px;
            margin-bottom: 0;
            & span {
                font-weight: 500;
                font-size: 20px;
                color: var(--temnyy-100);
            }
        }
    }
}
@media (max-width: 668px) {
    .intro {
        padding-top: 20px;
        &__text {
            gap: 16px;
        }
        &__title {
            margin-bottom: 16px;
        }
        &__subtitle {
            margin-bottom: 30px;
            max-width: 100%;
        }
    
        &__btn {
            width: 100%;
        }
    
        &__picture {
            max-width: 320px;
            display: flex;
            align-items: center;
            justify-content: center;
            & img {
                width: 100%;
                object-fit: cover;
            }
        }
    }
}