
// gallery
.gallery {
    overflow: hidden;

    & .title-h2 {
        max-width: 65%;
    }

    &__swiper {
        overflow: visible;
        & .swiper-wrapper {
            margin-bottom: 32px;
        }
    }

    &__swiper-btns {
        justify-content: center;
    }


    &__swiper-slide {

        & .gallery__card {
            border-radius: 10px;
            padding: 40px;
            background: var(--seryy);
            transition: transform 0.3s ease, scale 0.3s ease;
            transform: scale(0.76);
            transform-origin: top center;
        }

        &.swiper-slide-active {
            & .gallery__card {
                transform: scale(1); 
                z-index: 2;
            }
        }
    }

    &__image {
        display: block;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100%;
        height: 400px;
        max-height: 400px;
        margin-bottom: 32px;
        transition: all 0.8s ease-in;
        
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.8s ease-in;
        }
    }

    &__label {
        color: var(--temnyy-60);
        &_name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 10px;
        }
    }
}

.gallery-page__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    & .gallery__image  {
        max-height: 260px;
        margin-bottom: 0;
    }
}

// media gallery
@media (max-width: 1200px) {
    .gallery__title {
        flex-direction: column;
        gap: 30px;
        align-items: stretch;
        & .title-h2 {
            max-width: 100%;
        }
    }
    .gallery__tab-btns > button {
        flex: 1 0 auto;
    }
    .gallery__image {
        height: 300px;
        max-height: 300px;
    }
}
@media (max-width: 1024px) {
    .gallery__image {
        height: 240px;
        max-height: 240px;
    }
    .gallery-page__cards {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }
}
@media (max-width: 768px) {
    .gallery {
        .gallery__title {
            gap: 20px;
        }
        &__swiper {
            & .swiper-wrapper {
                margin-bottom: 0;
            }
        }
        &__swiper-btns {
            display: none;
        }
    
        &__swiper-slide {
            & .gallery__card {
                padding: 20px;
                transform: scale(1); 
                display: flex;
                flex-direction: column-reverse;
                gap: 40px;
            }
        }
    
        &__image {
            height: 180px;
            max-height: 180px;
            margin-bottom: 0;
        }
    
        &__label {
            &_name {
                font-size: 20px;
            }
        }
    }
    .gallery-page__cards {
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
    }
}
@media (max-width: 500px) {
    .gallery-page__cards {
        grid-template-columns: repeat(1, 1fr);
        gap: 16px;
    }
}