	--plashki: #424e59;
	--izobrazhenie: #d2dce8;
	--fon-plashek: #eaedf2;
	--temnyy: #080b0f;
	--tekst: #003649;
	--tekst-svetlyy: #405f6a;
	--belyy: #fff;
	--tegi: #556473;
	--bg-svetlo-seryy: #f9f9f9;
	--color-temno-zelenyy: #206175;
