
// stocks
.stocks {
    &__swiper .swiper-wrapper {
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        grid-auto-flow: column;
        //gap: 20px;
    }

    &__swiper-slide {
        height: auto;
        margin-top: 0 !important;

        &:first-child {
            grid-column: span 2;

            & .stocks__card {
                width: calc(200% + 20px);
                background: var(--osnovnoy-100) url(./../img/icons/stock-bg.svg) no-repeat;
                background-position: 100% 0;

                & .stocks__card_tag {
                    background: var(--belyy-20-obvodka-hover);
                    color: var(--belyy-100);
                    &.stock::before {
                        background: url("./../img/icons/stock-white.svg") no-repeat;
                        background-size: contain;
                    }
                    &.discount::before {
                        background: url("./../img/icons/discount-white.svg") no-repeat;
                        background-size: contain;
                    }
                }

                & .stocks__card_name {
                    color: var(--belyy-100);
                    font-size: 32px;
                    max-width: 70%;
                }
            }
        }

        &:nth-child(1), &:nth-child(4), &:nth-child(6) {
            margin-bottom: 20px !important;
        }
    }

        
    &__card {
        border-radius: 10px;
        padding: 32px;
        background: var(--belyy-100);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 24px;
        height: 100%;
        min-height: 320px;

        &_tag {
            border-radius: 10px;
            padding: 12px 16px;
            background: var(--osnovnoy-5);
            font-weight: 500;
            font-size: 16px;
            color: var(--osnovnoy-100);
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 8px;

            &::before {
                content: "";
                display: inline-block;
                width: 14px;
                height: 14px;
            }

            &.stock::before {
                background: url("./../img/icons/stock.svg") no-repeat;
                background-size: contain;
            }
            &.discount::before {
                background: url("./../img/icons/discount.svg") no-repeat;
                background-size: contain;
            }
        }

        &_name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 20px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
        }

        &_btn {
            padding: 0;
            & a {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__btn-mobile {
        display: none;
    }
}

.stocks-page__cards {
    & .stocks__swiper-slide {
        &:first-child {
            & .stocks__card {
                width: 100%;
            }
        }
        &:nth-child(1), &:nth-child(4), &:nth-child(6) {
            margin-bottom: 0 !important;
        }
    }
}

// media stocks
@media (max-width: 1150px) {
    .stocks {
        &__swiper .swiper-wrapper {
            grid-template-rows: repeat(1, 1fr);
        }
    
        &__swiper-slide {
            margin-top: 0 !important;
            &:first-child {
                grid-column: span 1;
    
                & .stocks__card {
                    width: 100%;
                    background: var(--osnovnoy-100);
                    & .stocks__card_name {
                        font-size: 20px;
                        max-width: 100%;
                    }
                }
            }
    
            &:nth-child(1), &:nth-child(4), &:nth-child(6) {
                margin-bottom: 0 !important;
            }
        }
    }
}
@media (max-width: 768px) {
    .stocks {
        &__swiper {
            margin-bottom: 20px;
        }

        &__swiper-slide {
            &:first-child {
                & .stocks__card {
                    width: 100%;
                    & .stocks__card_name {
                        font-size: 20px;
                        max-width: 100%;
                    }
                }
            }
        }
            
        &__card {
            padding: 20px;
            gap: 20px;
            min-height: 200px;
    
            &_tag {
                padding: 8px 12px;
                font-size: 14px;
                &::before {
                    width: 12px;
                    height: 12px;
                }
            }
            &_btn {
                padding: 0;
            }
        }

        &__btn-mobile {
            display: block;
        }
    }
}