
/* Общие glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html.burger-lock::before {
    opacity: 1;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

body {
    margin: 0;
    //overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    background-color: #fff;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: -0.02em;
    color: var(--temnyy-100);
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}

.section-offset {
    margin-bottom: 200px;
}

.section-fon {
    padding: 200px 0;
    background: var(--seryy);
}

.section__title {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 60px;

    & .title-h2 {
        max-width: 50%;
    }
}

.page__top {
    border-bottom: 1px solid var(--temnyy-10);
    padding: 32px 0 52px;
    margin-bottom: 80px;
    &_inner {
        display: flex;
        flex-direction: column;
        gap: 100px;
    }
}



/*  buttons */
.primary-btn, .secondary-btn, .button-arrow__btn {
    display: block;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: -0.02em;
    text-align: center;

    border-radius: 10px;
    padding: 21px 36px;

    position: relative;
    z-index: 1;
    overflow: hidden;
    transition: .2s;
    &::before {
        content: "";
        background:  rgba(8, 11, 15, 0.2);
        position: absolute;
        z-index: -1;
        padding: 0.85em 0.75em;
        display: block;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        transform: scale(0, 1);
        transition: all 0.3s ease;
    }
    & span {
        position: relative;
        z-index: 2;
    }
    &:active {
        transform: scale(0.95);
    }
    &:hover {
        background-position: 0;
    }
}
@media (hover: hover) {
    .primary-btn, .secondary-btn, .tertiary-btn {
        &:hover::before {
            transform: scale(1, 1);
            transition: all 0.3s ease-out;
        }
    }
}

.primary-btn {
    color: var(--belyy-100);
    background: var(--osnovnoy-100);
    position: relative;
    overflow: hidden;
    &::before {
        background: rgba(255, 255, 255, 0.2);
    }
}

.secondary-btn {
    border: 1px solid var(--osnovnoy-100);
    //background: var(--belyy-100);
    background: transparent;
    color: var(--osnovnoy-100);
    z-index: 1;
    &::before {
        background: var(--osnovnoy-5);
    }
    &:hover {
        border: 1px solid transparent;
    }
}

.button-arrow {
    display: flex;
    gap: 6px;
    cursor: pointer;

    &:hover {
        & .button-arrow__icon {
            border: 1px solid var(--osnovnoy-100);
            background: var(--belyy-100);
            & svg path {
                stroke: var(--osnovnoy-100);
            }
        }
    }

    &__btn {
        border: 1px solid var(--osnovnoy-100);
        background: var(--belyy-100);
        color: var(--osnovnoy-100);
        padding: 21px;
    }

    &__icon {
        content: "-";
        display: block;
        min-height: 100%;
        width: 100%;
        max-width: 68px;
        border-radius: 10px;
        background: var(--osnovnoy-100);
        display: flex;
        align-items: center;
        justify-content: center;
        & svg path {
            transition: all 0.3s ease;
        }
    }
}

.button-arrow.white {
    &:hover {
        & .button-arrow__icon {
            border: 1px solid var(--belyy-100);
            background: var(--osnovnoy-100);
            & svg path {
                stroke: var(--belyy-100);
            }
        }
    }

    & .button-arrow__btn {
        border: 1px solid var(--belyy-100);
        background: transparent;
        color: var(--belyy-100);
    }

    & .button-arrow__icon {
        background: var(--belyy-100);
    }
}



.btn-arrow {
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid var(--osnovnoy-100);
        border-right: 2px solid var(--osnovnoy-100);
        transform: rotate(45deg);
        transition: all 0.3s ease;
    }
}

.btn-more.active {
    &::after {
        transform: rotate(225deg);
    }
}

.btn-show {
    margin: 0 auto;
    margin-top: 40px;
}



/*  titles  */

.title-h1, .title-h2, .title-h3, .title-h4, .title-h5, .title-h6 {
    font-family: var(--second-family);
    font-weight: 600;
    line-height: 105%;
    letter-spacing: -0.01em;
    color: var(--temnyy-100);

    & span {
        color: var(--osnovnoy-100);
    }
}
.title-h1 {
    font-size: 80px;
}
.title-h2 {
    font-size: 60px;
}
.title-h3 {
    font-size: 48px;
}
.title-h4 {
    font-size: 32px;
}
.title-h5 {
    font-size: 24px;
}


/*  form  */

.input-block {
    position: relative;

    & input, & textarea {
        width: 100%;
        max-width: 100%;
        cursor: pointer;
        outline: none;
        border: 1px solid var(--temnyy-10);
        border-radius: 10px;
        padding: 48px 36px 20px 20px;
        background: var(--seryy);
        font-weight: 400;
        font-size: 18px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--temnyy-100);
        transition: all 0.3s ease;

        &::placeholder {
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;
            letter-spacing: -0.02em;
            color: var(--temnyy-100);
        }

        &:focus {
            border: 1px solid var(--temnyy-50-obvodka);
        }
    }

    & label {
        display: block;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--temnyy-60);
        position: absolute;
        top: 20px;
        left: 20px;
        right: 20px;
        z-index: 2;
        pointer-events: none;
    }
}

.input-block.input-white {
    & input, & textarea {
        background: var(--belyy-10);
        border: 1px solid var(--belyy-10);
        color: var(--belyy-100);
        &::placeholder {
            color: var(--belyy-100);
        }
        &:focus {
            border: 1px solid var(--belyy-20-obvodka-hover);
        }
    }
    & label {
        color: var(--belyy-70);
    }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}

textarea {
    width: 100%;
    min-height: 180px;
    resize: none;
}

.politic {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: -0.02em;
    color: var(--temnyy-60);
    & a {
        color: inherit;
        text-decoration: underline;
        transition: all 0.3s ease;
        &:hover {
            color: var(--temnyy-100);
        }
    }

    &.politic-white {
        color: var(--belyy-70);
            & a:hover {
                color: var(--belyy-100);
            }
    }
}



/*  search  */
.form-search {
    position: relative;
    width: 100%;
    & input {
        opacity: 0.7;
        width: 100%;
        &:focus {
            opacity: 1;
        }
    }
    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}

/* select */


.select-block {
    position: relative;

    & select {
        width: 100%;
        max-width: 100%;
        cursor: pointer;
        outline: none;
        border: 1px solid var(--temnyy-10);
        border-radius: 10px;
        padding: 48px 36px 20px 20px;
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        appearance: none;
        background: var(--seryy) url(./../img/icons/arrow-bottom.svg) no-repeat right;
        background-position-x: calc(100% - 20px);
        background-position-y: calc(100% - 22px);
        transition: all 0.3s ease;

        &:focus {
            background-color: #fff;
        }
    }

    & label {
        display: block;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--temnyy-50-obvodka);
        position: absolute;
        top: 20px;
        left: 20px;
        right: 20px;
        z-index: 2;
        pointer-events: none;
    }
}


.sort {
    &__text {
        margin-bottom: 20px;
    }

    &__item {
        margin-bottom: 20px;
        display: flex;
        gap: 14px;
        align-items: center;

        & input[type="checkbox"] {
            position: relative;
            width: 20px;
            height: 20px;
            color: #000;
            border: 1px solid var(--osnovnoy-100);
            border-radius: 4px;
            appearance: none;
            outline: 0;
            cursor: pointer;
            transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
            &::before {
                position: absolute;
                content: '';
                display: block;
                top: 3px;
                left: 6.5px;
                width: 6px;
                height: 10px;
                border-style: solid;
                border-color: #fff;
                border-width: 0 2px 2px 0;
                transform: rotate(45deg);
                opacity: 0;
            }
            &:checked {
                color: #fff;
                border-color: var(--osnovnoy-100);
                background: var(--osnovnoy-100);
                &::before {
                    opacity: 1;
                }
            }
        }
        & label {
            position: relative;
            color: var(--temnyy-100);
            &::before {
                position: absolute;
                content: attr(data-content);
            }
        }
    }
}



/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotate(225deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab__btns {
    border: 1px solid var(--temnyy-10);
    border-radius: 10px;
    padding: 4px;
    display: flex;
    gap: 4px;
}
.tab__btn {
    padding: 16px 24px;
    border-radius: 6px;
    font-weight: 500;
    transition: all 0.3s ease;
    &:hover {
        background: var(--osnovnoy-5);
    }
    &.active {
        color: var(--belyy-100);
        background: var(--osnovnoy-100);
    }
}

.tab__btns-2 {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
}
.tab__btn-2 {
    border: 1px solid var(--osnovnoy-10);
    border-radius: 10px;
    padding: 16px 24px;
    font-weight: 500;
    transition: all 0.3s ease;
    &:hover {
        border: 1px solid var(--osnovnoy-100);
    }
    &.active {
        border: 1px solid var(--osnovnoy-100);
        color: var(--osnovnoy-100);
    }
}

.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}



/* rating */
.full-stars {
    text-align: center;
}
.full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.full-stars input {
    position: absolute;
    left: -9999px;
}
.full-stars label {
    cursor: pointer;
}
.full-stars label svg {
    margin: 0 2px;
    height: 20px;
    width: 20px;
    & path {
        fill: var(--belyy-100);
        fill-opacity: 1;
        -webkit-transition: fill 0.3s;
        -o-transition: fill 0.3s;
        transition: fill 0.3s;

    }
}
.full-stars input:checked ~ label svg path {
    opacity: 0.2;
}
.full-stars .rating-group:hover label svg path {
    fill: var(--belyy-100);
    opacity: 1;
}
.full-stars .rating-group input:hover ~ label svg path {
    opacity: 0.2;
}


/* breadcrumbs */
.breadcrumbs__wrap {
    margin-top: 32px;
}
.breadcrumbs, .breadcrumbs li {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 8px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.breadcrumbs li, .breadcrumbs li a {
    font-size: 16px;
    color: var(--temnyy-100);
}
.breadcrumbs li a {
    color: var(--temnyy-60);
}
.breadcrumbs li a:hover {
    color: var(--temnyy-100);
}
.breadcrumbs li:after {
    content: "—";
    display: block;
    font-size: 20px;
    color: var(--temnyy-60);
    font-size: inherit;
}
.breadcrumbs li:last-child::after {
    content: none;
}

.article {
    &__items {
        display: flex;
        gap: 12px;
        max-width: 1200px;
        margin-top: -60px;
    }
    &__item {
        flex: 1 0 30%;
        border: 1px solid var(--temnyy-20-obvodka-hover-);
        border-radius: 10px;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        color: var(--temnyy-60);
        position: relative;
        padding-left: 56px;
        & span {
            font-weight: 500;
            color: var(--temnyy-100);
        }
        & img {
            width: 24px;
            height: 24px;
            object-fit: contain;
            position: absolute;
            top: 50%;
            left: 20px;
            transform: translateY(-50%);
        }
    }
}


/*  media global  */
@media (max-width: 1024px) {
    .article {
        &__items {
            flex-direction: column;
        }
    }
}
@media (max-width: 768px) {
    body {
        font-size: 16px;
    }
    .section-offset {
        margin-bottom: 80px;
    }
    .section-fon {
        padding: 80px 0;
    }
    .section__title {
        margin-bottom: 30px;
        & .title-h2 {
            max-width: 100%;
        }
    }
    .page__top {
        padding: 16px 0 28px;
        margin-bottom: 40px;
        &_inner {
            gap: 60px;
        }
    }

    .title-h1 {
        font-size: 29px;
    }
    .title-h2 {
        font-size: 26px;
    }
    .title-h3 {
        font-size: 24px;
    }
    .title-h4 {
        font-size: 20px;
    }
    .title-h5 {
        font-size: 18px;
    }


    .primary-btn, .secondary-btn, .button-arrow__btn {
        font-size: 16px;
        padding: 19px 22px;
    }
    .button-arrow__btn {
        width: 100%;
    }
    .button-arrow__icon {
        max-width: 60px;
    }

    .input-block {
        position: relative;

        & input {
            padding: 40px 30px 16px 20px;
            font-size: 16px;
            &::placeholder {
                font-size: 16px;
            }
        }
        & label {
            font-size: 14px;
            top: 16px;
        }
    }

    .select-block {
        & select {
            padding: 40px 30px 16px 20px;
        }
        & label {
            font-size: 14px;
            top: 16px;
        }
    }

    .tab__btn {
        font-size: 14px;
    }

    .tab__btns-2 {
        display: flex;
        flex-wrap: nowrap;
        gap: 8px;
        max-width: 100%;
        overflow-x: auto;
    }
    .tab__btn-2 {
        border: 1px solid var(--osnovnoy-10);
        border-radius: 10px;
        padding: 12px 20px;
        min-width: fit-content;
    }

    .article {
        &__items {
            margin-top: -40px;
            gap: 8px;
        }
        &__item {
            padding: 16px;
            padding-left: 44px;
            & img {
                width: 20px;
                height: 20px;
                left: 16px;
            }
        }
    }






    .sort {
        &__text {
            margin-bottom: 14px;
        }
        &__item {
            margin-bottom: 14px;
            & input[type="checkbox"] {
                width: 22px;
                height: 22px;
                &::before {
                    top: 3px;
                    left: 7px;
                    width: 6px;
                    height: 10px;
                    border-width: 0 2px 2px 0;
                }
            }
            & label {
                font-size: 16px;
            }
        }
    }

    .breadcrumbs__wrap {
        margin-top: 16px;
    }
    .breadcrumbs, .breadcrumbs li {
        gap: 6px;
    }
    .breadcrumbs li, .breadcrumbs li a {
        font-size: 14px;
    }
}


@media (max-width: 500px) {
    .btn-show {
        width: 100%;
        margin-top: 16px;
    }
}

