
// advantages
.advantages {
    &__list {
        & ul {
            display: flex;
            flex-wrap: wrap;

            & li {
                flex: 1 0 15%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                gap: 20px;
                min-height: 380px;
                padding: 40px 0px;
                color: var(--temnyy-60);

                &:not(:last-child) {
                    padding-right: 30px;
                    border-right: 1px solid var(--temnyy-10);
                    margin-right: 30px;
                }

                & p {
                    font-family: var(--second-family);
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 105%;
                    letter-spacing: -0.01em;
                    color: var(--temnyy-100);
                    position: relative;
                    padding-left: 56px;

                    &::before {
                        content: "";
                        display: block;
                        width: 40px;
                        height: 22px;
                        background: url("./../img/icons/list-item.svg") no-repeat center / contain;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }
        }
    }
}

// media advantages
@media (max-width: 1260px) {
    .advantages {
        &__list ul {
            & li {
                flex: 1 0 30%;
                &:nth-child(3) {
                    padding-right: 0;
                    border-right: none;
                    margin-right: 0;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .advantages {
        &__list {
            & ul {
                flex-direction: column;
    
                & li {
                    gap: 12px;
                    min-height: auto;
                    padding: 0;
    
                    &:nth-child(n) {
                        padding-right: 0;
                        border-right: none;
                        margin-right: 0;
                    }

                    &:not(:last-child) {
                        padding-bottom: 16px;
                        border-bottom: 1px solid var(--temnyy-10);
                        margin-bottom: 16px;
                    }
    
                    & p {
                        font-size: 18px;
                        padding-left: 44px;
                        &::before {
                            width: 32px;
                            height: 18px;
                        }
                    }
                }
            }
        }
    }
}