/* header  */

.header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    padding: 32px 0;
    //padding-top: 28px;
    background-color: #fff;
    border-bottom: 1px solid var(--temnyy-10);
    transition: all 0.3s ease;

    &.pad {
        padding: 10px 0;
        //padding: 0;
    }

    &.shrink .service-submenu {
        top: 133px;
    }

    &--active {
        position: fixed;
    }
}


.header__logo {
    display: block;
    overflow: hidden;
    max-width: 212px;
    max-height: 48px;
    min-width: 149px;
    min-height: 34px;
    width: 100%;
}

.logo__picture {
    width: 100%;
    height: 100%;
    & img {
        width: 100%;
        height: 100%;
    }
}

.header__main, .header__top {
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}


/* header__top  */

.header__top {
    padding-bottom: 28px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    transition: all 0.3s ease;
    overflow-y: hidden;

    max-height: 500px; /* достаточная высота */
    opacity: 1;

    &.hide {
        padding: 0;
    }
}

.header__info {
    display: flex;
    align-items: center;
    gap: 60px;
}

.header__info_item {
    display: flex;
    gap: 12px;
}

.header__info_icon {
    border-radius: 5px;
    width: 32px;
    height: 32px;
    background: var(--osnovnoy-5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__info_text {
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: -0.02em;
    color: var(--temnyy-100);
    
    & a, & button {
        font-weight: 500;
        color: var(--temnyy-100);
        transition: all 0.3s ease;
    }

    & a:hover, & button:hover {
        color: var(--osnovnoy-100);
    }
}


.header__networks {
    display: flex;
    gap: 8px;
}
.header__network {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 10px;
    background: var(--osnovnoy-5);
    transition: all 0.3s ease;

    & svg path {
        transition: all 0.3s ease;
    }
}
.header__network:hover {
    background: var(--osnovnoy-100);
    & svg path {
        fill: #fff;
    }
}

.header__search {
    padding: 10px;
    transition: all 0.3s ease;
    &:hover {
        opacity: 0.6;
    }
}


/* header__main */
.header__main {
    //padding: 28px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}

.header__menu {
    display: contents;
}

.header__buttons {
    display: flex;
    align-items: center;
    gap: 30px;
}

.header__btns {
    display: flex;
    gap: 10px;
    & button {
        padding: 21px 32px;
    }
}

.header__nav > ul {
    display: flex;
    align-items: center;
    gap: 40px;
}
.header__nav_item > a, .header__nav_item > button {
    font-weight: 500;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: -0.02em;
    color: var(--temnyy-100);

    padding-top: 10px;
    padding-bottom: 10px;
    &:hover {
        color: var(--osnovnoy-100);
    }
}

.header__nav_about > button {
    display: flex;
    align-items: center;
    gap: 10px;
    &::after {
        content: "";
        display: block;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid var(--osnovnoy-100);
        border-right: 2px solid var(--osnovnoy-100);
        transform: rotate(45deg);
    }
}

.header__nav_about.hide-item--active > button::after {
    transform: rotate(225deg);
}

.header__nav_service > button {
    border-radius: 10px;
    padding: 12px 20px;
    background: var(--seryy);
    border: 1px solid transparent;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease;
    &:hover {
        border: 1px solid var(--temnyy-20-obvodka-hover-);
    }
}
.header__nav_service-span {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 17px;
    height: 20px;
}
.header__nav_service-span > span {
    display: block;
    border-radius: 5px;
    width: 18px;
    height: 2px;
    background: var(--osnovnoy-100);
    transition: all 0.3s ease;
}




/*  header__submenu */
.header__submenu {
    display: none;
    z-index: 100;
}
.hide-item {
    position: relative;
}
.hide-item--active {
    .header__submenu {
        display: block;
    }
}
.hide-item--active.header__nav_service > button {
    color: var(--temnyy-100);
    border: 1px solid var(--temnyy-20-obvodka-hover-);
    .header__nav_service-span span {
        background: var(--temnyy-100);
    }
    .header__nav_service-span span:first-child {
        -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        position: relative;
        top: 6px;
    }
    .header__nav_service-span span:nth-child(2) {
        opacity: 0;
    }
    .header__nav_service-span span:last-child {
        -webkit-transform: rotate(-45deg);
                -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
        position: relative;
        bottom: 7px;
    } 
}





/* mobile__wrapper */
.header-mobile__wrapper {
    display: none;
    //width: 100%;
}

.header-mobile__burger-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 15px;
}

.header-mobile__eye {
    order: 6;
    text-align: right;
    margin-top: -45px;
    & button {
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.6;
        }
    }
}

.header-mobile__info {
    order: 5;
}


/* burger__menu */
.burger__menu {
    width: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.hamRotate.active {
    transform: rotate(45deg);
}
.hamRotate180.active {
    transform: rotate(180deg);
}
.line {
    fill:none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke:var(--temnyy-100);
    stroke-width:5.5;
    stroke-linecap:round;
}
.ham4 .top {
    stroke-dasharray: 40 121;
}
.ham4 .bottom {
    stroke-dasharray: 40 121;
}
.ham4.active .top {
    stroke-dashoffset: -68px;
}
.ham4.active .bottom {
    stroke-dashoffset: -68px;
}


// media header-1
@media (max-width: 1460px) {
    .header-mobile__wrapper {
        display: block;
    }

    .header__inner {
        display: flex;
        flex-direction: column-reverse;
    }
    .header__info, .header__networks, .header__nav,  .header__btns, .header__buttons  {
        display: none;
    }
    .header {
        padding: 20px 0;
    }
    .header__top {
        display: none;
    }

    .header__menu {
        display: none;
    }
    .header__menu--active {
        position: fixed;
        top: 90px;
        right: 0;
        width: 100%;
        z-index: 10;
        height: calc(100% - 90px);
        overflow-y: auto;
        padding: 40px 20px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        gap: 32px;
        &::-webkit-scrollbar {
            width: 3px;
        }
        &::-webkit-scrollbar-track {
            background: #DCDCDD;
            border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background: var(--osnovnoy-100);
        }

        & .header__top {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            justify-content: flex-start;
            gap: 20px;
            padding: 20px;
        }
        & .header__nav {
            display: block;
            opacity: 1 !important;
            order: 1;
        }

        & .header__info {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            order: 3;
            gap: 0;
        }
        & .header__info_item {
            padding-bottom: 16px;
            border-bottom: 1px solid var(--temnyy-10);
            margin-bottom: 16px;
        }
        & .header__info_text {
            & p {
                font-weight: 400;
                font-size: 16px;
                line-height: 130%;
                letter-spacing: -0.02em;
                color: var(--temnyy-50-obvodka);
                margin-bottom: 10px;
            }
            & a, & button {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 105%;
                letter-spacing: -0.01em;
                text-align: left;
                color: var(--temnyy-100);
            }
        }

        & .header__networks {
            display: flex;
            justify-content: start;
            order: 4;
        }
        & .header__network {
            background: var(--osnovnoy-100);
        }
    }

    .header__nav {
        display: none;
        & > ul {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            -webkit-box-align: stretch;
                -ms-flex-align: stretch;
                    align-items: stretch;
            gap: 0;
            margin-bottom: 0;
            position: relative;
            & > li > a {
                display: block;
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 20px;
                line-height: 105%;
                letter-spacing: -0.01em;
                color: var(--temnyy-100);
                padding: 16px 0 0 0;
                border-top: 1px solid var(--temnyy-10);
                margin-top: 16px;
            }
        }
    }
    .header__nav_about {
        & button {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 20px;
            line-height: 105%;
            padding: 16px 0 0 0;
            border-top: 1px solid var(--temnyy-10);
            margin-top: 16px;
            position: relative;
            padding-right: 36px;

            &::before {
                content: "";
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 5px;
                min-width: 28px;
                width: 28px;
                height: 28px;
                background: var(--osnovnoy-100);
                position: absolute;
                top: 10px;
                right: 0;
            }
            &::after {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-bottom: 2px solid var(--belyy-100);
                border-right: 2px solid var(--belyy-100);
                transform: rotate(45deg);
                position: absolute;
                top: 17px;
                right: 9.5px;
                z-index: 2;
            }
        }
    }
    .header__nav_about.hide-item--active > button::after {
        top: 20px;
    }

    .header__nav_service button {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 20px;
        line-height: 105%;
        padding: 0;
        background: transparent;
        border-color: transparent !important;
        position: relative;
        padding-right: 36px;

        &::before {
            content: "";
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 5px;
            min-width: 28px;
            width: 28px;
            height: 28px;
            background: var(--osnovnoy-100);
            position: absolute;
            top: -4px;
            right: 0;
        }
        &::after {
            content: "";
            display: block;
            width: 8px;
            height: 8px;
            border-bottom: 2px solid var(--belyy-100);
            border-right: 2px solid var(--belyy-100);
            transform: rotate(45deg);
            position: absolute;
            top: 5px;
            right: 9.5px;
            z-index: 2;
        }
    }
    
    .header__nav_service.hide-item--active > button::after {
        transform: rotate(225deg);
        top: 8px;
    }
    
    .header__nav_service-span {
        display: none;
    }
    .header__nav_list {
        position: relative;
        z-index: 10;
    }


    .header__search {
        &_form {
            position: relative;
            width: 100%;
            margin-bottom: 26px;
            display: flex;
            gap: 10px;
            & input {
                border: 1px solid var(--temnyy-10);
                border-radius: 10px;
                padding: 20px 24px;
                background: var(--seryy);
                width: 100%;
                color: var(--temnyy-100);
                transition: all 0.2s ease-in;
    
                &::placeholder {
                    color: var(--temnyy-60);
                }
                &:focus {
                    border: 1px solid var(--temnyy-50-obvodka);
                }
            }
            & img {
                position: absolute;
                top: 30px;
                right: 23px;
            }
        }

        &_search {
            display: none !important;
        }
    }
}

@media (max-width: 800px) {
    .header__logo {
        max-width: 149px;
    }
}



