
// popup-widget 
.popup-widget {
    position: fixed;
    bottom: 244px;
    right: 0;
    border-radius: 10px 0 0 10px;
    border: 1px solid #ffd1d5;
    overflow: hidden;
    z-index: 1000;
    transition: all 0.3s ease;
    
    &__link {
        border-bottom: 1px solid #ffd1d5;
        padding: 2px 24px;
        //width: 66px;
        height: 66px;
        display: flex;
        align-items: center;
        gap: 16px;
        font-size: 16px;
        color: var(--temnyy-100);
        background: #fef3f4;
        width: 100%;
        transition: all 0.3s ease;
        & svg path {
            transition: all 0.3s ease;
        }
        
        & p {
            transition: all 0.3s ease;
            min-width: fit-content;
            width: 100%;
        }

        &.btn-close {
            background: var(--osnovnoy-100);
            color: var(--belyy-100);
        }
        
        &:not(.btn-close):hover {
            background: var(--osnovnoy-100);
            color: var(--belyy-100);
            & svg path {
                fill: var(--belyy-100);
            }
        }

        &_icon {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 18px;
        }
    }

    &.close {
        max-width: 66px;
        & .popup-widget__link {
            max-width: 66px;
            height: 66px;
            & p {
                min-width: fit-content;
                opacity: 0;
                transform: translateX(100%);
                transition: all 0.3s ease;
            }
            &.btn-close {
                & .popup-widget__link_icon {
                    transform: rotate(180deg);
                }
            }
        }
    }
}


// media popup-widget
@media (max-width: 1400px) {
    .popup-widget {
        bottom: 80px;
        max-width: 100%;
        &__link {
            max-width: 100%;
            padding: 2px 16px;
            height: 50px;
            gap: 10px;
            font-size: 14px;
            transition: all 0.3s ease;
            
            & p {
                transition: all 0.3s ease;
                min-width: fit-content;
                width: 100%;
            }

            &.btn-close {
                background: var(--osnovnoy-100);
                color: var(--belyy-100);
            }
            


            &_icon {
                min-width: 16px;
            }
        }

        &.close {
            border: 1px solid transparent;
            max-width: 44px;
            & .popup-widget__link {
                max-width: 44px;
                height: 50px;
                & p {
                    min-width: fit-content;
                    opacity: 0;
                    transform: translateX(100%);
                    transition: all 0.3s ease;
                }
                &:not(.btn-close) {
                    max-width: 0;
                    position: relative;
                    left: 100%;
                }

                &.btn-close {
                    border-radius: 10px 0 0 10px;
                }
            }
        }
    }
}