
// faq
.faq {
    &__row {
        display: flex;
        align-items: start;
        gap: 20px;
    }

    &__list {
        flex: 1 0 45%;
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__item {
        border-radius: 10px;
        padding: 40px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.06);
        background: var(--belyy-100);
    }

    &__question {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 24px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--temnyy-100);

        & p {
            margin: auto 0;
        }
    }

    &__btn {
        border-radius: 10px;
        max-width: 60px;
        width: 100%;
        aspect-ratio: 1/1;
        height: 60px;
        background: var(--osnovnoy-5);
        border: 1px solid transparent;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;

        &:hover {
            border: 1px solid rgba(247, 23, 41, 0.5);
        }

        & span {
            display: inline-block;
            width: 10px;
            height:10px;
            border-bottom: 2px solid var(--osnovnoy-100);
            border-right: 2px solid var(--osnovnoy-100);
            transform: rotate(45deg);
            transition: all 0.3s ease;
        }
    }

    &__answer {
        padding-top: 20px;
        margin-top: 20px;
        border-top: 1px solid var(--temnyy-10);
        color: var(--temnyy-60);
    }


    &__form {
        flex: 1 0 45%;
        border-radius: 10px;
        padding: 48px;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.08);
        background: var(--osnovnoy-100);

        &_top {
            padding-bottom: 24px;
            border-bottom: 1px solid var(--belyy-20-obvodka-hover);
            margin-bottom: 24px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--belyy-100);
            margin-bottom: 16px;
        }

        &_subtitle {
            color: var(--belyy-70);
            margin-bottom: 24px;
        }

        &_doctor {
            border-radius: 10px;
            padding: 16px 20px 16px 16px;
            background: var(--belyy-100);
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 16px;

            & span {
                font-size: 16px;
                color: var(--temnyy-60);
            }

            & p {
                font-weight: 500;
            }

            & picture {
                display: block;
                position: relative;
                &::after {
                    content: "";
                    display: block;
                    width: 12px;
                    height: 12px;
                    border-radius: 100%;
                    background:  #1cb738;
                    outline: 3px solid #fff;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                }
            }

            & img {
                display: block;
                min-width: 60px;
                width: 60px;
                aspect-ratio: 1/1;
                object-fit: cover;
                object-position: top;
                border-radius: 100%;
                background: var(--seryy);
                position: relative;

            }
        }

        &_inner {
            display: flex;
            flex-wrap: wrap;
            gap: 16px 20px;

            & > * {
                flex: 1 0 45%;
            }

            & > .textarea-block {
                flex: 1 0 100%;
            }
        }
    }
}

.faq-page {
    &__tab {
        margin-bottom: 40px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px 20px;
    }
    
    &__tab-btns {
        margin-bottom: 60px;
    }

    &__form {
        display: flex;
        gap: 33px;

        & .faq__form_top {
            flex: 1 0 45%;
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
            padding-right: 33px;
            border-right: 1px solid var(--belyy-20-obvodka-hover);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }
}

// media faq
@media (max-width: 1260px) {
    .faq {
        &__row {
            flex-direction: column;
            align-items: stretch;
            gap: 40px;
        }
    }
}
@media (max-width: 1100px) {
    .faq-page {
        &__list {
            grid-template-columns: repeat(1, 1fr);
            gap: 16px 20px;
        }
        
        &__form {
            flex-direction: column;
            gap: 24px;

            & .faq__form_top {
                padding-right: 0;
                border-right: none;
                padding-bottom: 24px;
                border-bottom: 1px solid var(--belyy-20-obvodka-hover);
            }
        }
    }
}
@media (max-width: 768px) {
    .faq {
        &__container {
            display: flex;
            flex-direction: column;
        }
        & .section__title {
            display: contents;
        }
        &__row {
            display: flex;
            align-items: start;
            gap: 20px;
            display: contents;
        }

        &__title {
            margin-bottom: 30px;
        }

        &__top_btn {
            order: 2;
            margin-bottom: 30px;
        }
    
        &__list {
            gap: 12px;
            order: 1;
            margin-bottom: 16px;
        }
        &__item {
            padding: 20px;
        }
    
        &__question {
            font-size: 16px;
        }
        &__btn {
            max-width: 48px;
            height: 48px;
            & span {
                width: 7px;
                height: 7px;
            }
        }
        &__answer {
            padding-top: 16px;
            margin-top: 16px;
            font-size: 14px;
        }
    
    
        &__form {
            order: 3;
            padding: 40px 20px;
            &_title {
                font-size: 20px;
                margin-bottom: 10px;
            }

            &_doctor {
                padding: 12px;
                & span {
                    font-size: 14px;
                }
                & picture {
                    &::after {
                        width: 8px;
                        height: 8px;
                    }
                }
                & img {
                    min-width: 44px;
                    width: 44px;
    
                }
            }
    
            &_inner {
                display: flex;
                flex-wrap: wrap;
                gap: 16px 20px;
    
                & > * {
                    flex: 1 0 45%;
                }
            }
        }
    }

    .faq-page {
        &__tab {
            margin-bottom: 60px;
        }
        &__tab-btns {
            margin-bottom: 32px;
        }
    }
}
@media (max-width: 560px) {
    .faq {
        &__form {
            max-width: 380px;
            margin: 0 auto;
            &_inner {
                gap: 16px;
    
                & > * {
                    flex: 1 0 100%;
                }
            }
        }
    }
}