
// dropper
.dropper {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }
    &__swiper-slide {
        height: auto;
    }

    &__card {
        border-radius: 10px;
        padding: 20px;
        background: var(--belyy-100);
        height: 100%;

        &_top {
            margin-bottom: 36px;
        }

        &_text {
            color: var(--temnyy-60);
            margin-bottom: 12px;
        }

        &_name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 16px;
        }

        &_price {
            & p {
                display: none;
            }
            & span {
                display: block;
                border: 1px solid var(--osnovnoy-20);
                border-radius: 10px;
                padding: 12px 20px;
                font-weight: 500;
                font-size: 20px;
                color: var(--osnovnoy-100);
                width: fit-content;
            }
        }

        &_content {
            display: flex;
            gap: 20px;
        }

        &_info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
            max-width: 50%;
        }

        &_structure {
            & p {
                font-size: 16px;
                color: var(--temnyy-60);
                margin-bottom: 12px;
            }

            & ul {
                display: flex;
                flex-wrap: wrap;
                gap: 8px;
                & li {
                    border-radius: 10px;
                    padding: 10px 16px;
                    background: var(--osnovnoy-5);
                    font-size: 16px;
                    color: var(--osnovnoy-100);
                }
            }
        }

        &_btns {
            display: flex;
            gap: 10px;
            & > * {
                flex: 1 0 45%;
            }
        }

        &_img {
            display: block;
            margin: 0 auto;
            position: relative;
            z-index: 2;
            &::after {
                content: "";
                display: block;
                width: 308px;
                height: 176px;
                background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="308" height="176" viewBox="0 0 308 176"><path d="M306.09 51.6073C319.364 95.8445 262.031 147.999 178.034 168.099C94.0363 188.198 15.183 168.63 1.90964 124.393C-11.3637 80.1555 45.9693 28.0005 129.966 7.90142C213.964 -12.1977 292.817 7.3701 306.09 51.6073Z" fill="%23F6F6F6"/></svg>');
                background-size: contain;
                background-repeat: no-repeat;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: -1;
            }
        }
    }
}

// media dropper
@media (max-width: 768px) {
    .dropper {
        &__swiper-btns {
            display: none;
        }
        
        &__card {
            &_top {
                margin-bottom: 16px;
            }
    
            &_text {
                font-size: 14px;
                margin-bottom: 8px;
            }
    
            &_name {
                font-size: 20px;
                padding-bottom: 14px;
                border-bottom: 1px solid rgba(31, 31, 31, 0.06);
                margin-bottom: 14px;
            }
    
            &_price {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 20px;
                & p {
                    display: block;
                    font-size: 14px;
                    color: var(--temnyy-60);
                }
                & span {
                    display: block;
                    border: none;
                    border-radius: 0;
                    padding: 0;
                    font-size: 16px;
                }
            }
    
            &_content {
                flex-direction: column;
                gap: 0;
            }
    
            &_info {
                display: contents;
            }
    
            &_structure {
                order: 3;
                & p {
                    font-weight: 500;
                    color: var(--temnyy-100);
                    margin-bottom: 12px;
                }
    
                & ul li {
                    padding: 8px 12px;
                    font-size: 14px;
                }
            }
    
            &_btns {
                order: 2;
                padding-bottom: 20px;
                border-bottom: 1px solid rgba(31, 31, 31, 0.06);
                margin-bottom: 20px;
            }
    
            &_img {
                margin-bottom: 33px;
                & img {
                    max-height: 200px;
                }
                &::after {
                    width: 243px;
                    height: 139px;
                }
            }
        }
    }
}
