
// doctors
.doctors {
    overflow: hidden;

    &__wrap {
        padding: 60px;
        border-radius: 10px;
        background: var(--osnovnoy-100) url("./../img/icons/docotor-bg.svg") no-repeat center / cover;
        background-size: auto 100%;
        background-position: 60%;
        display: flex;
        justify-content: space-between;
        gap: 40px;
        color: var(--belyy-100);
        position: relative;
    }

    &__content {
        flex-basis: 33%;
    }
    
    &__fio {
        color: var(--belyy-100);
        padding-bottom: 24px;
        border-bottom: 1px solid var(--belyy-20-obvodka-hover);
        margin-bottom: 24px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 60px;
        & li {
            padding-left: 36px;
            position: relative;
            &::before {
                content: "";
                display: block;
                width: 24px;
                height: 24px;
                position: absolute;
                top: 0;
                left: 0;
            }
            &:nth-child(1)::before {background: url("./../img/icons/doctor-bag.svg") no-repeat center / contain;}
            &:nth-child(2)::before {background: url("./../img/icons/doctor-expiriense.svg") no-repeat center / contain;}
            &:nth-child(3)::before {background: url("./../img/icons/doctor-stepen.svg") no-repeat center / contain;}
        }
        & p {
            font-size: 16px;
            color: var(--belyy-70);
        }
        & span {
            font-weight: 500;
        }
    }

    &__btns {
        display: flex;
        gap: 8px;
        & a {
            border: 1px solid var(--belyy-100);
        }
        & button {
            background: #fff;
            border: 1px solid #fff;
        }
    }

    &__photo {
        flex: 1 0 30%;
        display: flex;
        justify-content: start;
        align-items: end;
        width: 100%;
        margin-bottom: -60px;
        & img {
            width: 100%;
            height: 100%;
            max-width: 600px;
            max-height: 530px;
            object-fit: cover;
            object-position: top;
        }
    }

    &__ratings {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__rating {
        border: 1px solid var(--belyy-20-obvodka-hover);
        border-radius: 10px;
        padding: 20px;
        backdrop-filter: blur(94px);
        background: rgba(255, 255, 255, 0.01);
        &_top {
            display: flex;
            align-items: center;
            gap: 8px;
            font-weight: 500;
            font-size: 20px;
            margin-bottom: 17px;
        }
        &_stars {
            display: flex;
            gap: 4px;
            & svg {
                min-width: 14px;
            }
        }
    }


    &__swiper-btns {
        position: absolute;
        bottom: 0;
        right: 0;
        padding: 20px 0 0 20px;
        background-color: #fff;
        z-index: 2;
        border-radius: 10px 0 0 0;
    }

    & .swiperDoctors {
        overflow: visible;
        margin-top: 32px;
        padding-bottom: 20px;
    }

    &__card {
        border-radius: 10px;
        padding: 20px;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.08);
        background: var(--belyy-100);
        border: 1px solid transparent;
        min-height: 150px;
        position: relative;
        overflow: hidden;

        &::after {
            content: "";
            display: block;
            width: 15px;
            height: 8px;
            border-radius: 9px / 5px;
            background: var(--osnovnoy-100);
            opacity: 0.1;
            transform: rotate(-10deg);
            position: absolute;
            bottom: 20px;
            left: 20px;
        }

        &_name {
            font-weight: 500;
            color: var(--temnyy-100);
            max-width: 60%;
        }

        &_img {
            position: absolute;
            bottom: 0;
            right: 20px;
            width: 110px;
            height: 110px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
                position: relative;
                z-index: 2;
            }

            &::after {
                content: "";
                display: block;
                width: 90px;
                height: 155px;
                border-radius: 50px / 90px;
                background: var(--osnovnoy-20);
                filter: blur(54px);
                position: absolute;
                bottom: -30%;
                right: 10%;
                
            }
        }

        &.swiper-slide-thumb-active {
            border: 1px solid var(--osnovnoy-100);
            &::after {
                opacity: 1;
            }
        }
    }
}


.doctors-page__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}
.doctors-page__card {
    border: 1px solid var(--osnovnoy-10);
    border-radius: 10px;
    padding: 40px;
    padding-bottom: 0;
    background: var(--belyy-100);
    background: var(--belyy-100) url(./../img/icons/docotor-bg-mini-white.svg) no-repeat;
    background-position: top 0px right -220px;
    color: var(--temnyy-100);
    position: relative;
    overflow: hidden;

    & .doctors__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    & .doctors__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        margin-bottom: 40px;
        max-width: 60%;
    }
    
    & .doctors__fio {
        font-size: 32px;
        color: var(--temnyy-100);
        padding-bottom: 24px;
        border-bottom: 1px solid var(--temnyy-10);
        margin-bottom: 24px;
    }

    & .doctors__info {
        margin-bottom: 0px;
        & li {
            &:nth-child(1)::before {background: url("./../img/icons/doctor-bag-red.svg") no-repeat center / contain;}
            &:nth-child(2)::before {background: url("./../img/icons/doctor-expiriense-red.svg") no-repeat center / contain;}
            &:nth-child(3)::before {background: url("./../img/icons/doctor-stepen-red.svg") no-repeat center / contain;}
        }
        & p {
            color: var(--temnyy-60);
        }
    }

    & .doctors__btns {
        & button {
            border: 1px solid var(--osnovnoy-100);
        }
    }

    & .doctors__photo {
        position: absolute;
        bottom: 0;
        right: 10px;
        margin-bottom: 0px;
        justify-content: flex-end;
        & img {
            max-width: 400px;
            max-height: 340px;
        }
    }
}

.doctors-page__card.main-doctor {
    background: var(--osnovnoy-100);
    background: var(--osnovnoy-100) url(./../img/icons/docotor-bg-mini.svg) no-repeat;
    background-position: top 0px right -220px;
    color: var(--belyy-100);

    & .doctors__fio {
        color: var(--belyy-100);
        border-bottom: 1px solid var(--belyy-20-obvodka-hover);
    }

    & .doctors__info {
        & li {
            &:nth-child(1)::before {background: url("./../img/icons/doctor-bag.svg") no-repeat center / contain;}
            &:nth-child(2)::before {background: url("./../img/icons/doctor-expiriense.svg") no-repeat center / contain;}
            &:nth-child(3)::before {background: url("./../img/icons/doctor-stepen.svg") no-repeat center / contain;}
        }
        & p {
            color: var(--belyy-100);
        }
    }

    & .doctors__btns {
        & button {
            border: 1px solid #fff;
        }
    }
}



// media doctors
@media (max-width: 1400px) {
    .doctors-page__card {
        & .doctors__photo {
            position: absolute;
            bottom: 0;
            right: -20px;
            margin: 0;
            & img {
                max-width: 350px;
                max-height: 340px;
            }
        }
    }
}
@media (max-width: 1200px) {
    .doctors {
        &__content {
            flex: 1 0 40%;
        }
    
        &__photo {
            flex: 1 0 40%;
        }
    
        &__ratings {
            display: none;
        }
    }

    .doctors-page__cards {
        grid-template-columns: repeat(1, 1fr);
        gap: 20px;
        & .doctors__btns {
            flex-direction: row;
        }
    }
}
@media (max-width: 860px) {
    .doctors {
        &__wrap {
            background: var(--osnovnoy-100) url("./../img/icons/docotor-bg-mobile.svg") no-repeat center / cover;
            background-size: 100% auto;
            background-position: bottom;
            flex-direction: column;
            gap: 26px;
        }

        &__info {
            flex-direction: row;
            gap: 48px;
            margin-bottom: 26px;
            & li {
                flex: 1 0 40%;
                padding-left: 0;
                &::before {
                    content: none;
                }
                &:nth-child(3) {display: none;}
            }
        }
    
        &__btns {
            & a, & button {
                flex-grow: 1;
            }
        }
    
        &__photo {
            justify-content: center;
            & img {
                max-width: 460px;
                max-height: 400px;
                height: 400px;
            }
        }

        &__swiper-btns {
            display: none;
        }
    }

    .doctors-page__card {
        padding: 40px;
        padding-bottom: 0;
        display: flex;
        flex-direction: column;
        background-position: bottom 120% right 50%;
        &.main-doctor {
            background-position: bottom 120% right 50%;
        }
        & .doctors__row {
            display: contents;
        }

        & .doctors__content {
            flex-basis: auto;
            gap: 20px;
            margin-bottom: 20px;
            max-width: 100%;
        }

        & .doctors__photo {
            position: static;
            flex: 1 0 40%;
            justify-content: center;
            & img {
                max-width: 240px;
                max-height: 250px;
            }
        }
    }
}

@media (max-width: 768px) {
    .doctors {
        &__wrap {
            padding: 20px;
        }

        &__fio {
            font-size: 20px;
            padding-bottom: 12px;
            margin-bottom: 12px;
        }
    
        &__info {
            & p, & span {
                font-size: 14px;
            }
        }
    
        &__photo {
            justify-content: center;
            margin-bottom: -20px;
            & img {
                max-width: 300px;
                max-height: 250px;
                height: 250px;
            }
        }

        & .swiperDoctors {
            margin-top: 24px;
        }
    
        &__card {
            padding: 12px;
            min-height: 130px;
    
            &::after {
                width: 11px;
                height: 6px;
                border-radius: 9px / 5px;
                bottom: 12px;
                left: 12px;
            }
    
            &_name {
                font-size: 14px;
                max-width: 100%;
            }
    
            &_img {
                right: 4px;
                width: 80px;
                height: 80px;
    
                &::after {
                    width: 76px;
                    height: 134px;
                    bottom: -40%;
                }
            }
        }
    
    }

    .doctors-page__card {
        padding: 20px;
        padding-bottom: 0;

        & .doctors__content {
            flex-basis: auto;
            gap: 20px;
            margin-bottom: 20px;
        }
        
        & .doctors__fio {
            font-size: 20px;
            padding-bottom: 12px;
            margin-bottom: 12px;
        }

        & .doctors__photo {
            & img {
                max-width: 240px;
                max-height: 250px;
            }
        }
    }
}