// popup-search

.popup-search {
    &__body {
        display: flex;
        justify-content: flex-end;
        align-items: stretch;
        padding: 0;
    }
    &__content {
        padding: 70px 0;
        box-shadow: 0 4px 40px 0 rgba(0, 0, 0, 0.06);
        background: var(--belyy);
        max-width: 100%;
        height: max-content;
        max-height: 100vh;
    }

    &__form {
        position: relative;
        width: 100%;
        padding-bottom: 40px;
        border-bottom: 1px solid var(--temnyy-10);
        margin-bottom: 40px;
        display: flex;
        gap: 10px;
        & input {
            border: 1px solid var(--temnyy-10);
            border-radius: 10px;
            padding: 20px 24px;
            background: var(--seryy);
            width: 100%;
            color: var(--temnyy-100);
            transition: all 0.2s ease-in;

            &::placeholder {
                color: var(--temnyy-60);
            }
            &:focus {
                border: 1px solid var(--temnyy-50-obvodka);
            }
        }
        & img {
            position: absolute;
            top: 30px;
            right: 95px;
        }
    }



    &__search {
        margin-top: 40px;
        &-result {
            max-height: 666px;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        & li a {
            display: block;
            border-radius: 10px;
            padding: 12px 20px;
            background: var(--osnovnoy-5);
            color: var(--osnovnoy-100);
            transition: all 0.3s ease;
            &:hover {
                background: var(--osnovnoy-10);
            }
        }
    }

    &__list_result {
        padding-right: 12px;
        & li a {
            display: block;
            padding: 16px 0;
            border-bottom: 1px solid var(--temnyy-10);
            position: relative;
            padding-right: 62px;
            &:hover::after {
                background: var(--osnovnoy-100);
            }
            & p {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 20px;
                line-height: 105%;
                letter-spacing: -0.01em;
                color: var(--temnyy-100);
                margin-bottom: 8px;
            }
            & span {
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--osnovnoy-100);
            }

            &::after {
                content: "";
                display: block;
                width: 52px;
                height: 52px;
                border-radius: 6px;
                background: var(--seryy);
                background-image: var(--seryy) url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="23" height="20" viewBox="0 0 23 20" fill="none"><path d="M1 10L21 10M21 10L12.1111 19M21 10L12.1111 1" stroke="%23F71729" stroke-width="2" stroke-linecap="round"/></svg>');
                background-size: contain;
                background-repeat: no-repeat;
                transition: all 0.2s ease-in;
                position: absolute;
                top: 16px;
                right: 0;
            }
        }
    }

    &__close {
        width: 65px;
        height: 65px;
        border-radius: 10px;
        border: 1px solid var(--temnyy-10);
        position: static;
        display: flex;
        align-items: center;
        justify-content: center;
        & img {
            position: static;
            height: auto;
        }
    }
}

// media popup-search
@media (max-width: 1460px) {
    .popup-search {
        &__body {
            display: flex;
            justify-content: flex-end;
            align-items: stretch;
            padding: 0;
        }
        &__content {
            padding: 70px 0;
            box-shadow: 0 4px 40px 0 rgba(0, 0, 0, 0.06);
            background: var(--belyy);
            max-width: 100%;
            height: max-content;
            max-height: 100vh;
        }

        &__form {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 26px;
            & input {
                padding: 20px 24px;
            }
            & img {
                position: absolute;
                top: 30px;
                right: 23px;
            }
        }


        &__search {
            margin-top: 40px;
            &-result {
                max-height: 460px;
                width: 100%;
            }
        }

        &__list_result {
            width: 100%;
            padding-right: 12px;
            & li a {
                padding-right: 0;
                &:hover::after {
                    background: var(--osnovnoy-100);
                }
                & p {
                    font-size: 18px;
                }
                & span {
                    font-size: 16px;
                }

                &::after {
                    content: none;
                }
            }
        }
    }
}
