/* swiper CUSTOM */

.swiper {
    width: 100%;
    height: 100%;
}

/* swiper-buttons */
.swiper-btns {
    display: flex;
    align-items: center;
    gap: 12px;
}
.swiper-button-next, .swiper-button-prev,
.swiper-button-next--white, .swiper-button-prev--white {
    position: static;
    margin: 0;
    min-width: 60px;
    width: 60px;
    height: 60px;
    border-radius: 10px;
    background: transparent;
    border: 1px solid var(--temnyy-20-obvodka-hover-);
    transition: all 0.3s ease;
}

.swiper-button-next:after, .swiper-button-prev:after {
    content: "";
    display: inline-block;
    width: 17px;
    height: 14px;
    background-size: contain;
    background-repeat: no-repeat;
}
.swiper-button-next:after {
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="17" height="14" viewBox="0 0 17 14" fill="none"><path d="M1 7L15 7M15 7L8.77778 13M15 7L8.77778 1" stroke="%231F1F1F" stroke-width="1.5" stroke-linecap="round"/></svg>');
}
.swiper-button-prev:after {
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="17" height="14" viewBox="0 0 17 14" fill="none"><path d="M16 7L2 7M2 7L8.22222 13M2 7L8.22222 1" stroke="%231F1F1F" stroke-width="1.5" stroke-linecap="round"/></svg>');
}
.swiper-button-next:hover,.swiper-button-prev:hover {
    border: 1px solid var(--temnyy-100);
}





/* swiper-buttons--white */
.swiper-button-next--white, .swiper-button-prev--white {
    background: var(--belyy);
}
.swiper-button-next--white:after, .swiper-button-prev--white:after {
    color: var(--color-temno-zelenyy);
}
.swiper-button-next--white:hover,.swiper-button-prev--white:hover {
    background: rgba(61, 124, 133, 0.25);
}
.swiper-button-next--white:hover:after, .swiper-button-prev--white:hover:after {
    color: var(--color-temno-zelenyy);
}


/* swiper-pagination  */
.swiper-pagination {
    display: none;
    position: static;
    width: auto !important;
    margin-top: 20px;

    &-bullet {
        position: static;
        border-radius: 10px;
        width: 8px;
        height: 8px;
        background: var(--osnovnoy-100);

        &-active {
            position: relative;
            &::after {
                content: "";
                display: block;
                width: 16px;
                height: 16px;
                border-radius: 10px;
                border: 1px solid var(--osnovnoy-100);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                
            }
        }
    }
}

@media (max-width: 768px) {
    .swiper-pagination {
        display: block;
    }
}



.swiper-pagination-fraction {
    font-weight: 700;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);

    .swiper-pagination-current {
        font-weight: 700;
        font-size: 26px;
        color: var(--belyy);
    }
}

.swiper-scrollbar {
    position: static !important;
    background: var(--seryy);
    border-radius: 10px;
    margin-top: 30px;
    &-drag {
        background: var(--osnovnoy-100);
        border-radius: 10px;
    }
}

