
// quality
.quality {
    & .title-h2 {
        max-width: 1100px;
    }
    &__wrapper {
        border-radius: 10px;
        padding: 20px;
        background: linear-gradient(180deg, #9ebac8 0%, #9fbbc8 9%, #89a6b4 38.61%, #7b9ba8 50.85%, #708c9a 71.18%, #6b8896 87.8%, #6e8a98 98.13%, #6d8998 99.64%);
        position: relative;
        overflow: hidden;
    }

    &__picture {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 70%;
        }
    }

    &__inner {
        border-radius: 5px;
        padding: 40px;
        background: var(--belyy-100);
        max-width: 50%;
        position: relative;
        z-index: 2;
    }

    &__text {
        position: relative;
        margin-bottom: 100px;
        & p {
            font-style: italic;
            font-size: 20px;
            color: var(--temnyy-100);
            margin-bottom: 16px;
        }

        &_signature {
            text-align: right;
            margin-left: auto;
        }
    }

    &__author {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;

        &_fio {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            margin-bottom: 8px;
        }

        &_post {
            font-size: 16px;
            color: var(--temnyy-60);
        }

        &_infos {
            display: flex;
            gap: 10px;
        }

        &_info {
            border-radius: 10px;
            padding: 12px 16px;
            background: var(--seryy);
            & p {
                font-size: 16px;
                color: var(--temnyy-60);
                //margin-bottom: 4px;
            }
            & span {
                font-weight: 500;
                font-size: 18px;
            }
        }
    }
}

// media quality
@media (max-width: 1100px) {
    .quality {
        &__wrapper {
            padding: 10px;
            padding-top: 400px;
        }
    
        &__picture {
            bottom: auto;
            & img {
                object-position: 100%;
                min-height: 450px;
            }
        }
        &__inner {
            max-width: 100%;
        }
    }
}
@media (max-width: 768px) {
    .quality {

        &__wrapper {
            padding: 5px;
            padding-top: 350px;
        }

        &__picture {
            & img {
                min-height: 370px;
            }
        }

        &__inner {
            padding: 20px;
        }
    
        &__text {
            margin-bottom: 40px;
            & p {
                font-size: 16px;
                margin-bottom: 162x;
            }
    
            &_signature {
                max-width: 144px;
                object-fit: cover;
            }
        }
    
        &__author {
            flex-direction: column;
            align-items: stretch;
            gap: 16px;
    
            &_fio {
                font-size: 24px;
                margin-bottom: 4px;
            }
    
            &_post {
                font-size: 14px;
            }
    
            &_info {
                flex: 1 0 40%;
                padding: 12px;
                & p {
                    font-size: 14px;
                }
                & span {
                    font-size: 16px;
                }
            }
        }
    }
}
@media (max-width: 430px) {
    .quality {
        &__wrapper {
            padding-top: 310px;
        }
    
        &__picture {
            bottom: auto;
            & img {
                object-position: 90%;
                min-height: 330px;
            }
        }
    }
}