
// form
.form {
    background: var(--osnovnoy-100);
    position: relative;
    overflow: hidden;

    &__elipse {
        content: "";
        display: block;
        width: 912px;
        height: 520px;
        border-radius: 400px/250px;
        background:var(--belyy-10);
        transform: rotate(-15deg);
        position: absolute;
    }
    &__elipses-top {
        & span {
            &:nth-child(1) {
                top: -100%;
                left: -55px;
            }
            &.animation-active:nth-child(1) {
                top: -324px;
            }
        }
        & span:nth-child(2) {
            top: -354px;
            left: -55px;
        }
    }
    &__elipses-bottom {
        & span {
            width: 1166px;
            height: 609px;
            border-radius: 560px/300px;

            &:nth-child(1) {
                bottom: -324px;
                right: -96px;
            }
            &:nth-child(2) {
                bottom: -354px;
                right: -96px;
            }
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__wrap {
        max-width: 50%;
        padding: 160px 0;
    }

    &__titles {
        margin-bottom: 80px;
        color: var(--belyy-70);
        position: relative;
        z-index: 2;
    }

    &__title {
        color: var(--belyy-100);
        margin-bottom: 20px;
    }

    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 20px;
        & > * {
            flex: 1 0 45%;
        }
    }

    &__btn {
        width: 100%;
    }

    &__picture {
        width: 100%;
        max-width: 740px;
        max-height: 80%;
        display: flex;
        justify-content: flex-end;
        align-items: end;
        margin-bottom: -2px;
        position: relative;
        z-index: 2;
        & img {
            max-height: 650px;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: bottom;
        }
    }
}

// media form
@media (max-width: 1300px) {
    .form {
        background: var(--osnovnoy-100);
        position: relative;
        overflow: hidden;

        &__row {
            flex-direction: column;
            gap: 40px;
        }

        &__wrap {
            max-width: 100%;
            padding: 100px 0 0 0;
        }

        &__picture {
            max-width: 600px;
            margin: 0 auto;
        }
    }
}

@media (max-width: 768px) {
    .form {
        &__elipse {
            width: 568px;
            height: 325px;
            border-radius: 280px/180px;
            transform: rotate(-15deg);
        }
        &__elipses-top {
            & span {
                &:nth-child(1) {
                    top: -100%;
                    top: -220px;
                    left: -120px;
                }
                &.animation-active:nth-child(1) {
                    top: -220px;
                }
            }
            & span:nth-child(2) {
                top: -240px;
                left: -120px;
            }
        }
        &__elipses-bottom {
            & span {
                width: 620px;
                height: 354px;
                border-radius: 310px/160px;

                &:nth-child(1) {
                    bottom: -57px;
                    right: -150px;
                }
                &:nth-child(2) {
                    bottom: -77px;
                    right: -150px;
                }
            }
        }

        &__wrap {
            padding: 80px  0 0 0;
        }

        &__titles {
            margin-bottom: 40px;
        }

        &__title {
            margin-bottom: 16px;
        }

        &__inner {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 16px;
            & > * {
                flex: 1 0 100%;
            }
        }

        &__picture {
            max-width: 400px;
        }
    }
}