
// articles
.articles {

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 2fr);
        gap: 20px;
    }

    &__card {
        border-radius: 10px;
        padding: 40px;
        background: var(--belyy-100);
        color: var(--temnyy-60);
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 16px;
        }

        &_text {
            margin-bottom: 40px;
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            gap: 20px;
        }

        &_date {
            font-size: 16px;
            color: var(--osnovnoy-100);
            display: flex;
            align-items: center;
            gap: 10px;
            &::before {
                content: "";
                display: block;
                min-width: 17px;
                width: 17px;
                height: 18px;
                background: url("./../img/icons/calendar.svg") no-repeat center / contain;
            }
        }

        &_link {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 60px;
            height: 60px;
            border-radius: 6px;
            background: var(--seryy);
            transition: all 0.3s ease;
            & svg path {
                transition: all 0.3s ease;
            }
            &:hover {
                background: var(--osnovnoy-100);
                & svg path {
                    stroke: var(--belyy-100);
                }
            }
        }
    }
}

// media advantages
@media (max-width: 1024px) {
    .articles {
        &__cards {
            grid-template-columns: repeat(1, 2fr);
            gap: 20px;
        }
    }
}

@media (max-width: 768px) {
    .articles {
        &__cards {
            gap: 16px;
        }
        &__card {
            padding: 20px;
            &_title {
                font-size: 20px;
                margin-bottom: 8px;
            }
            &_text {
                margin-bottom: 24px;
            }
            &_date {
                font-size: 14px;
                gap: 8px;
                &::before {
                    min-width: 13px;
                    width: 13px;
                    height: 14px;
                }
            }
            &_link {
                width: 48px;
                height: 48px;
            }
        }
    }
}