
// licenses
.licenses {
    &__row {
        display: flex;
    }
    &__row .container {
        display: flex;
    }

    &__content {
        flex: 0 0 100%;
        display: flex;
    }
    
    &__left {
        flex: 1 0 40%;
        max-width: 50%;
        padding-right: 20px;
    }

    & .title-h2 {
        max-width: 60%;
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        gap: 12px;
        & li {
            font-weight: 500;
            font-size: 20px;
            border-radius: 10px;
            padding: 24px;
            background: var(--belyy-100);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;

            &::after {
                content: "";
                display: block;
                width: 40px;
                height: 22px;
                background: url("./../img/icons/list-item.svg") no-repeat center / contain;
            }
        }
    }

    &__right {
        flex: 0 0 50%;
        max-width: 50%;
        transform: translate(-100%, 0);
        border-radius: 10px 0 0 10px;
        background: var(--belyy-100);
        padding: 32px;
        overflow: hidden;
    }

    &__picture {
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            max-height: 550px;
            border-radius: 10px;
            object-fit: cover;
            object-position: top;
        }
    }

    &__swiper {
        position: relative;
    }

    &__swiper_label {
        width: 100%;
        max-width: 68%;
        font-weight: 500;
        font-size: 24px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--temnyy-100);
        position: absolute;
        bottom: 90px;
        left: calc(100% + 20px);
    }

    &__swiper-slide {
            height: fit-content;
            transform: scale(0.4) !important;
            transform-origin: top left;
            transition: all 0.4s ease;
            position: relative;

            & .licenses__swiper_label {
                display: none;
            }

            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 100%;
                background: #fff;
                opacity: 0.5;
                position: absolute;
                top: 0;
                left: 0;
                pointer-events: none;
                transition: all 0.4s ease;
            }
            
            &.swiper-slide-active  {
                transform: scale(1) !important;
                margin-right: 30px !important;

                & .licenses__swiper_label {
                    display: block;
                }

                &::after {
                    opacity: 0;
                }
        }
    }

    &__swiper-btns {
        position: absolute;
        bottom: 0px;
        left: calc(100% / 3 + 166px);
    }

    &__swiper-pagination {
        display: none;
    }
}

.licenses-page {
    &__card {
        border-radius: 10px;
        padding: 10px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.06);
        background: var(--belyy-100);

        & .licenses__picture {
            height: auto;
            margin-bottom: 10px;
            & img {
                max-height: 450px;
            }
        }
    }

    &__label {
        border-radius: 10px;
        padding: 20px 24px;
        background: var(--seryy);
        font-weight: 500;
        text-align: center;
        color: var(--temnyy-100);
    }
}

// media licenses
@media (max-width: 1580px) {
    .licenses {
        & .title-h2 {
            max-width: 100%;
        }
        &__swiper-btns {
            left: calc(100% / 3 + 200px);
        }
        &__swiper_label {
            max-width: 50%;
        }
    }
}

@media (max-width: 1200px) {
    .licenses {
        overflow: hidden;
        &__row {
            display: flex;
            flex-direction: column;
        }
        &__row .container {
            display: flex;
            flex-direction: column;
        }
        
        &__content {
            flex: 0 0 100%;
            display: flex;
            flex-direction: column;
        }
        
        &__left {
            flex: 1 0 40%;
            max-width: 100%;
            padding-right: 0;
            padding-bottom: 24px;
            border-bottom: 1px solid var(--temnyy-10);
            margin-bottom: 24px;
        }
    
        &__right {
            flex: 0 0 100%;
            max-width: 100%;
            transform: translate(0, 0);
            margin: 0 0 0 10px;
            padding: 0;
            background: transparent;
            border-radius: 0;
        }
    
        &__picture {
            & img {
                max-height: 450px;
                border-radius: 5px;
            }
        }

        &__swiper_label {
            border-radius: 10px;
            padding: 12px 20px;
            font-size: 20px;
            text-align: center;
            background: var(--seryy);
            position: static;
            max-width: 100%;
            margin-top: 10px;
        }
    
        &__swiper-slide {
                height: fit-content;
                transform: scale(1) !important;
                border-radius: 10px;
                padding: 10px;
                box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.06);
                background: var(--belyy-100);
                &::after {
                    content: none;
                }
    
                & .licenses__swiper_label {
                    display: block;
                }
                
                &.swiper-slide-active  {
                    margin-right: 20px !important;
                }
        }
    
        &__swiper-btns {
            display: none;
        }
        &__swiper-pagination {
            display: block;
        }
    }
}

@media (max-width: 768px) {
    .licenses {
        &__list ul {
            & li {
                font-size: 16px;
                padding: 20px;
                flex-direction: row-reverse;
                justify-content: flex-end;
                gap: 18px;
                &::after {
                    width: 32px;
                    height: 18px;
                }
            }
        }
        &__picture {
            & img {
                max-height: 280px;
            }
        }
        &__swiper_label {
            font-size: 16px;
        }
    }

    .licenses-page {
        &__card {
            & .licenses__picture {
                & img {
                    max-height: 320px;
                }
            }
        }
        &__label {
            padding: 12px 18px;
        }
    }
}
@media (max-width: 560px) {
    .licenses {
        &__swiper-slide {
            &.swiper-slide-active  {
                margin-right: 10px !important;
            }
        }
    }
}