// footer
.footer {
    padding: 120px 0 60px;
    background: #111;


    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
        padding-bottom: 42px;
        border-bottom: 1px solid var(--belyy-10);
        margin-bottom: 42px;
    }

    &__contant {

    }

    &__logo {
        display: block;
        max-width: 212px;
        margin-bottom: 36px;
    }

    &__networks {
        margin-bottom: 62px;
        &_row {
            display: flex;
            gap: 8px;
        }
    }

    &__network {
        border-radius: 10px;
        padding: 6px;
        width: 50px;
        height: 50px;
        background: var(--belyy-10);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.3s ease;
        & svg path {
            transition: all 0.3s ease;
        }
        &:hover {
            background: var(--belyy-100);
            & svg path {
                fill: var(--osnovnoy-100);
            }
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 24px;
    }

    &__text {
        font-size: 16px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--belyy-50);
        margin-bottom: 10px;
    }

    &__link {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 32px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--belyy-100);
        text-align: left;
        transition: all 0.2s ease-in;
        &:hover {
            opacity: 0.7;
        }
    }

    &__right {
        flex-basis: 50%;

        & .footer__text {
            margin-bottom: 62px;
        }
    }

    &__nav {
        display: flex;
        justify-content: space-between;
        gap: 32px;
        flex-wrap: wrap;
        &_item {
            flex: 1 0 45%;
        }
        &_link {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--belyy-100);
            transition: all 0.2s ease-in;
            &:hover {
                opacity: 0.7;
            }
        }
        & ul {
            margin: 20px 0;
            & a {
                display: block;
                font-weight: 400;
                font-size: 18px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--belyy-70);
                padding: 6px 0;
                transition: all 0.3s ease;
                &:hover {
                    color: var(--belyy-100);
                }
            }
        }
    }

    &__copy {
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: -0.02em;
        color: var(--belyy-50);
        & p:not(:last-child) {
            margin-bottom: 12px;
        }
        & a {
            font-weight: 500;
            font-size: 16px;
            text-decoration: underline;
            color: var(--belyy-100);
        }
    }
}


// media footer
@media (max-width: 1000px) {
    .footer {
        &__row {
            flex-direction: column;
            gap: 0;
        }
        &__contant {
            display: contents;
        }
        &__logo {
            margin-bottom: 30px;
        }
        &__networks {
            order: 4;
            margin-bottom: 0;
        }
        &__items {
            order: 3;
            margin-bottom: 24px;
        }
        &__right {
            order: 2;
            flex-basis: 100%;
            margin-bottom: 48px;
    
            & .footer__text {
                display: none;
            }
        }
    }
    
}
@media (max-width: 768px) {
    .footer {
        padding: 60px 0 40px;

        &__row {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 48px;
        }
    
        &__logo {
            max-width: 177px;
        }
    
        &__link {
            font-size: 18px;
        }
    
        &__nav {
            flex-direction: column;
            gap: 16px;
            &_item {
                flex: 1 0 100%;
                &:not(:last-child) {
                    padding-bottom: 16px;
                    border-bottom: 1px solid var(--belyy-10);
                }
            }
            &_link {
                font-size: 20px;

                &.footer__nav_acc {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    gap: 10px;
                    transition: all 0.4s ease;
                    &::after {
                        content: "";
                        display: inline-block;
                        width: 8px;
                        height: 8px;
                        border-top: 2px solid  var(--belyy-100);
                        border-right: 2px solid  var(--belyy-100);
                        transform: rotate(135deg);
                        transition: all 0.3s ease;
                    }
                    &.active {
                        margin-bottom: 12px;
                        &::after {
                            transform: rotate(315deg);
                        }
                    }
                }
            }

            & ul {
                margin: 0;
                max-height: 0;
                overflow: hidden;
                transition: all 0.3s ease;
                & a {
                    font-size: 16px;
                    padding: 4px 0;
                }
            }
        }
    }
}