
// services
.services {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        border: 1px solid var(--temnyy-5);
        border-radius: 10px;
        padding: 28px;
        background: var(--seryy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        height: 100%;

        &_top {
            display: flex;
            gap: 24px;
        }

        &_icon {
            display: block;
            min-width: 36px;
            width: 36px;
            height: 36px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &_name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 8px;
        }

        &_price {
            font-weight: 500;
            color: var(--osnovnoy-100);
        }

        &_list {
            margin-bottom: 20px;
            & ul li {
                &:not(:last-child) {
                    padding-bottom: 12px;
                    border-bottom: 1px solid rgba(31, 31, 31, 0.07);
                    margin-bottom: 12px;
                }
                & a {
                    display: flex;
                    gap: 45px;
                    color: var(--temnyy-100);
                    &::before {
                        content: "";
                        display: inline-block;
                        min-width: 17px;
                        width: 17px;
                        height: 15px;
                        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="17" height="15" viewBox="0 0 17 15" fill="none"><path d="M1 7.5L15 7.5M15 7.5L8.77778 14M15 7.5L8.77778 1" stroke="%23F71729" stroke-width="1.5" stroke-linecap="round"/></svg>');
                        background-size: contain;
                        background-repeat: no-repeat;
                        margin-top: 6px;
                        transition: all 0.2s ease-in;
                    }
                    &:hover::before {
                        transform: translateX(20px);
                    }
                }
            }
        }
    }
}

// media services
@media (max-width: 768px) {
    .services {
    
        &__swiper-btns {
            display: none;
        }
    
        &__card {
            padding: 20px;
            gap: 24px;

            &_icon {
                min-width: 24px;
                width: 24px;
                height: 24px;
            }
    
            &_name {
                font-size: 20px;
            }
    
            &_list {
                margin-bottom: 20px;
                & ul li {
                    &:not(:last-child) {
                        padding-bottom: 10px;
                        margin-bottom: 10px;
                    }
                    & a {
                        gap: 37px;
                        &::before {
                            min-width: 12px;
                            width: 12px;
                            height: 14px;
                            margin-top: 5px;
                        }
                        &:hover::before {
                            transform: translateX(20px);
                        }
                    }
                }
            }
        }
    }
}