
// park
.park {
    padding-bottom: 100px;
    
    &__swiper {
        padding-bottom: 48px;
        border-bottom: 1px dashed var(--temnyy-20-obvodka-hover-);
        margin-bottom: 28px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__image {
        display: block;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100%;
        height: 280px;
        max-height: 280px;
        
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 80px;
    }

    &__left {
        flex: 1 0 45%;
    }
    &__right {
        width: 100%;
        max-width: 50%;
    }
    &__content {
        flex: 1 0 45%;
    }

    &__text {
        color: var(--temnyy-60);
        margin-bottom: 24px;
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        gap: 16px;
        & li {
            display: flex;
            gap: 16px;
            padding-bottom: 16px;
            border-bottom: 1px solid var(--temnyy-10);
            
            &::before {
                content: "";
                display: block;
                width: 40px;
                height: 22px;
                background: url("./../img/icons/list-item.svg") no-repeat center / contain;
            }
        }
    }

    &__personal {
        & ol {
            display: flex;
            gap: 10px;
            list-style-type: none;
            counter-reset: item;
    
            & li {
                flex: 1 0 30%;
                border-radius: 10px;
                padding: 20px;
                background: var(--belyy-100);
                font-weight: 500;
                position: relative;
                overflow: hidden;
                min-height: 294px;
    
                & p {
                    max-width: 70%;
                }

                & span::before {
                    content: counter(item, decimal-leading-zero);
                    counter-increment: item;
                    font-weight: 500;
                    font-size: 16px;
                    color: var(--osnovnoy-100);
                }
            }
        }
        
        &_number {
            color: #d2d2d2;
            margin-bottom: 12px;
        }

        &_img {
            position: absolute;
            bottom: 0;
            right: 0;
        }
    }

    &__swiper-pagination {
        margin-bottom: 20px;
    }
}

// media park
@media (max-width: 1200px) {
    .park {
        overflow: hidden;

        & .title-h2 {
            max-width: 100%;
        }

        &__swiper {
            overflow: visible;
        }
        &__row {
            flex-direction: column;
            gap: 24px;
        }
        &__right {
            max-width: 100%;
        }
    }
}

@media (max-width: 768px) {
    .park {
        padding-bottom: 40px;

        &__swiper {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 24px;
        }
    
        &__image {
            height: 200px;
            max-height: 200px;
        }

        &__list ul {
            gap: 14px;
            & li {
                gap: 16px;
                padding-bottom: 14px;
                &::before {
                    width: 32px;
                    height: 18px;
                }
            }
        }
    
        &__personal {
            & ol {
                flex-direction: column;
                gap: 20px;
        
                & li {
                    padding: 16px;
                    overflow: visible;
                    min-height: auto;
        
                    & p {
                        max-width: 65%;
                    }
    
                    & span::before {
                        font-size: 14px;
                    }
                }
            }
            
            &_number {
                margin-bottom: 18px;
            }
    
            &_img {
                right: 6px;
                & img {
                    width: 100%;
                    height: 100%;
                    max-height: 94px;
                    object-fit: cover;
                    border-radius: 10px;
                }
            }
        }
    }
}