// problems
.problems {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__titles {
        flex: 1 0 45%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
    }

    &__title {
        max-width: 635px;
    }

    &__list {
        flex: 1 0 45%;
        & ul {
            & li {
                display: flex;
                gap: 20px;
                &:not(:last-child) {
                    padding-bottom: 24px;
                    border-bottom: 1px solid var(--temnyy-10);
                    margin-bottom: 24px;
                }
            }
        }

        &_icon {
            min-width: 60px;
            display: flex;
            align-items: center;
            gap: 9px;
            &::after, &::before {
                color: #d2d2d2;
                font-size: 28px;
            }
            &::after {
                content: ")";
            }
            &::before {
                content: "(";
            }
        }
    }
}

// media problems
@media (max-width: 1024px) {
    .problems {
        &__row {
            flex-direction: column;
            gap: 20px;
        }
    
        &__titles {
            display: contents;
        }
    
        &__title {
            //max-width: 100%;
            margin-bottom: 30px;
        }
    
        &__list {
            margin-bottom: 24px;
        }

        &__btn {
            order: 3;
        }
    }
}

@media (max-width: 768px) {
    .problems {
        &__row {
            gap: 0;
        }
        &__title {
            max-width: 100%;
        }
    
        &__list {
            & ul li {
                gap: 24px;
                &:not(:last-child) {
                    padding-bottom: 14px;
                    margin-bottom: 14px;
                }
            }
    
            &_icon {
                min-width: 46px;
                gap: 7px;
                & img {
                    max-width: 18px;
                    width: 100%;
                    object-fit: cover;
                }
                &::after, &::before {
                    font-size: 24px;
                }
            }
        }
    }
}