
// prices
.prices {
    &__title {
        margin-bottom: 32px;
    }

    &__tab {
        padding-bottom: 40px;
        border-bottom: 1px dashed var(--temnyy-20-obvodka-hover-);
        margin-bottom: 40px;
    }

    &__tab-btns {
        margin-bottom: 60px;
    }
    
    &__table {
        display: block;
        width: 100%;
        margin-bottom: 16px;

        &_body {
            display: block;
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 12px;
        }

        &_item {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 0 20px;
            justify-content: space-between;
            border: 1px solid var(--temnyy-20-obvodka-hover-);
            border-radius: 10px;
            padding: 28px;
            line-height: 140%;
            letter-spacing: -0.02em;
            transition: all 0.3s ease;
        }
        
        &_name {
            flex: 1 0 50%;
            font-weight: 500;
            font-size: 20px;

            & a {
                display: block;
                color: var(--temnyy-100);
                transition: all 0.2s ease-in;
                
                &:hover {
                    color: var(--osnovnoy-100);
                }
            }
        }

        &_cost {
            flex-basis: 17%;
            font-weight: 500;
            font-size: 20px;
            color: var(--osnovnoy-100);
        }

        &_btns {
            flex-basis: fit-content;
            &.accordion--active {
                & .accordion__btn-more {
                    color: var(--osnovnoy-100);
                    transform: none;
    
                    &::after {
                        transform: rotate(225deg);
                    }
                }
            }
        }

        &_btn {
            border-radius: 10px;
            padding: 10px 16px;
            background: var(--seryy);
            border: 1px solid transparent;
            transition: all 0.2s ease-in;

            &:hover {
                border: 1px solid var(--temnyy-20-obvodka-hover-);
            }
        }

        &_infos {
            flex-basis: 100%;
        }
        &_info {
            color: var(--temnyy-60);
            padding-top: 16px;
            border-top: 1px solid var(--temnyy-20-obvodka-hover-);
            margin-top: 16px;
        }

    }


    &__payments {
        & > p {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 24px;
        }

        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 12px;

            & li {
                flex-grow: 1;
                border-radius: 10px;
                padding: 24px;
                background: var(--osnovnoy-100);
                font-weight: 500;
                color: var(--belyy-100);
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 10px;
                position: relative;
                overflow: hidden;

                &::before {
                    content: "";
                    display: block;
                    width: 158px;
                    height: 90px;
                    background: var(--belyy-10);
                    border-radius: 100px/50px;
                    transform: rotate(-15deg);
                    position: absolute;
                    bottom: -9px;
                    right: -36px;
                }
            }
        }
    }
}

.prices-page__btn {
    width: 100%;
    margin-top: 16px;
}


// media prices
@media (max-width: 1000px) {
    .prices {
        &__payments ul li {
                    flex: 1 0 30%;
        }
    }
}
@media (max-width: 768px) {
    .prices {
        &__title {
            margin-bottom: 32px;
        }
    
        &__tab {
            padding-bottom: 40px;
            border-bottom: 1px dashed var(--temnyy-20-obvodka-hover-);
            margin-bottom: 40px;
        }
    
        &__tab-btns {
            margin-bottom: 60px;
        }
    
        &__table {
            &_item {
                padding: 20px;
            }
            &_name {
                flex: 1 0 100%;
                font-size: 18px;
                margin-bottom: 12px;
            }
            &_cost {
                flex:  1 0 50%;
                font-size: 18px;
            }
            &_btn {
                padding: 10px 14px;
                font-size: 14px;
                &::after {
                    width: 6px;
                    height: 6px;
                }
            }
            &_info {
                color: var(--temnyy-60);
                padding-top: 16px;
                border-top: 1px solid var(--temnyy-20-obvodka-hover-);
                margin-top: 16px;
            }
        }

        &__payments {
            & > p {
                font-size: 20px;
                margin-bottom: 20px;
            }
    
            & ul {
                gap: 10px;
                & li {
                    padding: 20px;
                    font-size: 14px;
                    flex-direction: column;
                    gap: 46px;
                    &::before {
                        min-width: 158px;
                        width: 120%;
                        height: 120px;
                        bottom: -52px;
                        right: -29px;
                    }
                }
            }
        }
    }
}