
// doctor

.doctor__title {
    margin-bottom: 60px;
}

.main-doctor__label {
    border: 1px solid var(--osnovnoy-20);
    border-radius: 10px;
    padding: 12px 20px;
    font-weight: 500;
    color: var(--osnovnoy-100);
    width: fit-content;
    margin: 0 auto;
    margin-bottom: 24px;
}

.doctor-info {
    &__row {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__content {
        border-radius: 10px;
        padding: 40px;
        background: var(--seryy);
        color: var(--temnyy-60);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 28px;
        & li {
            padding-left: 36px;
            position: relative;
            &:not(:last-child) {
                padding-bottom: 16px;
                border-bottom: 1px solid var(--temnyy-5);
            }

            &::before {
                content: "";
                display: block;
                width: 24px;
                height: 24px;
                position: absolute;
                top: 0;
                left: 0;
            }
            &:nth-child(1)::before {background: url("./../img/icons/doctor-bag-red.svg") no-repeat center / contain;}
            &:nth-child(2)::before {background: url("./../img/icons/doctor-expiriense-red.svg") no-repeat center / contain;}
            &:nth-child(3)::before {background: url("./../img/icons/doctor-stepen-red.svg") no-repeat center / contain;}
        }
        & p {
            font-size: 16px;
            color: var(--temnyy-60);
        }
        & span {
            font-weight: 500;
        }
    }

    &__ratings {
        border-radius: 10px;
        padding: 20px;
        background: var(--belyy-100);
        display: flex;
        gap: 16px;
    }

    &__rating {
        flex: 1 0 30%;
        &:not(:last-child) {
            padding-right: 16px;
            border-right: 1px solid var(--temnyy-5);
        }

        &_top {
            display: flex;
            align-items: center;
            gap: 6px;
            font-weight: 500;
            color: #2e3749;
            margin-bottom: 42px;

            &::after {
                content: ")";
                color: #d2d2d2;
                font-size: 22px;
            }
            &::before {
                content: "(";
                color: #d2d2d2;
                font-size: 22px;
            }
        }
    }

    &__photo {
        border: 1px solid var(--osnovnoy-20);
        border-radius: 10px;
        padding: 20px 10px 0px 20px;
        background: var(--belyy-100);
        background: var(--belyy-100) url(./../img/icons/docotor-bg-mini-white.svg) no-repeat;
        background-position: top 0% center;
        display: flex;
        align-items: end;

        &.main-doctor {
            background: var(--osnovnoy-100) url(./../img/icons/docotor-bg-mini.svg) no-repeat;
            background-position: top 0% center;
        }
    }

    &__img {
        display: block;
        width: 100%;
        height: 80%;
        & img {
            width: 100%;
            height: 100%;
            max-height: 625px;
            object-fit: cover;
        }
    }

    &__schedule {
        border-radius: 10px;
        padding: 40px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.06);
        background: var(--belyy-100);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 24px;
        }

        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 8px;
            & li {
                flex: 1 0 30%;
                max-width: calc(100% / 3 - 5px);
                border-radius: 10px;
                padding: 20px;
                background: var(--seryy);
                font-size: 16px;
                color: var(--temnyy-60);
                & span {
                    font-weight: 500;
                    color: var(--osnovnoy-100);
                }
            }
        }
    }

    &__price {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        font-size: 16px;
        color: var(--temnyy-60);
        margin-bottom: 16px;
        & span {
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            letter-spacing: -0.02em;
            color: var(--osnovnoy-100);
        }
    }

    &__btn {
        width: 100%;
        & p {
            width: 100%;
        }
    }
}

.doctor-education {
    overflow: hidden;
    &__list {
        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 40px;

            & li {
                max-width: calc(100% / 4 - 30px);
                padding-top: 22px;
                position: relative;

                &::after {
                    content: "";
                    display: block;
                    width: 16px;
                    height: 16px;
                    border-radius: 100%;
                    background: var(--belyy-100);
                    border: 5px solid var(--osnovnoy-100);
                    transform: translate(0, -50%);
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                &::before {
                    content: "";
                    display: block;
                    width: calc(100% + 12px);
                    height: 2px;
                    border-radius: 10px;
                    background: var(--temnyy-10);
                    position: absolute;
                    top: 0;
                    left: 22px;
                }
            }
        }

        &_top {
            display: flex;
            align-items: center;
            gap: 16px;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            letter-spacing: -0.02em;
            color: var(--osnovnoy-100);
            margin-bottom: 16px;

            & span {
                display: block;
                border: 1px solid var(--osnovnoy-10);
                border-radius: 10px;
                padding: 8px 12px;
                background: var(--osnovnoy-5);
            }
        }

    }
}

.doctor-diplomas {
    overflow: hidden;
    &__swiper {
        overflow: visible;
    }
}

.doctor-works {
    &__list {
        & ul {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 20px;

            & li a {
                border-radius: 10px;
                padding: 20px;
                background: var(--seryy);
                display: flex;
                gap: 20px;
                font-weight: 500;
                color: var(--temnyy-100);

                &::before {
                    content: "";
                    display: inline-block;
                    min-width: 63px;
                    width: 63px;
                    height: 63px;
                    border-radius: 6px;
                    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="63" height="63" viewBox="0 0 63 63" fill="none"><rect width="63" height="63" rx="6" fill="%23F71729"/><path fill-rule="evenodd" clip-rule="evenodd" d="M22.8017 21.7887C21.5 23.0775 21.5 25.1516 21.5 29.3V33.7C21.5 37.8483 21.5 39.9226 22.8017 41.2112C24.1035 42.5 26.1986 42.5 30.3889 42.5H32.6111C36.8013 42.5 38.8966 42.5 40.1982 41.2112C41.5 39.9226 41.5 37.8483 41.5 33.7V29.3C41.5 25.1516 41.5 23.0775 40.1982 21.7887C38.8966 20.5 36.8013 20.5 32.6111 20.5H30.3889C26.1986 20.5 24.1035 20.5 22.8017 21.7887ZM26.2222 27.1C26.2222 26.6444 26.5953 26.275 27.0556 26.275H35.9444C36.4047 26.275 36.7778 26.6444 36.7778 27.1C36.7778 27.5556 36.4047 27.925 35.9444 27.925H27.0556C26.5953 27.925 26.2222 27.5556 26.2222 27.1ZM26.2222 31.5C26.2222 31.0444 26.5953 30.675 27.0556 30.675H35.9444C36.4047 30.675 36.7778 31.0444 36.7778 31.5C36.7778 31.9556 36.4047 32.325 35.9444 32.325H27.0556C26.5953 32.325 26.2222 31.9556 26.2222 31.5ZM27.0556 35.075C26.5953 35.075 26.2222 35.4444 26.2222 35.9C26.2222 36.3556 26.5953 36.725 27.0556 36.725H32.6111C33.0713 36.725 33.4444 36.3556 33.4444 35.9C33.4444 35.4444 33.0713 35.075 32.6111 35.075H27.0556Z" fill="white"/></svg>');
                    background-size: contain;
