}




// media doctor
@media (max-width: 1500px) {
    .doctor-info {
        &__schedule {
            & ul {
                & li {
                    flex: 1 0 45%;
                    max-width: calc(100% / 2 - 5px);
                }
            }
        }
    }
}
@media (max-width: 1300px) {
    .doctor-info {
        &__row {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
    
        &__content {
            order: 2;
        }

        &__img {
            max-width: 500px;
            height: 100%;
            margin: 0 auto;
        }
    
        &__schedule {
            order: 3;
        }
    }

    .doctor-education {
        &__list {
            & ul {
                & li {
                    max-width: calc(100% / 3 - 27px);
                }
            }
        }
    }

    .doctor-works {
        &__list {
            & ul {
                grid-template-columns: repeat(3, 1fr);
                gap: 20px;
            }
        }
    }
}
@media (max-width: 1024px) {
    .doctor-education {
        &__list {
            & ul {
                & li {
                    max-width: calc(100% / 2 - 20px);
                }
            }
        }
    }
    .doctor-works {
        &__list {
            & ul {
                grid-template-columns: repeat(2, 1fr);
                gap: 20px;
            }
        }
    }
}
@media (max-width: 768px) {
    .doctor__title {
        margin-bottom: 30px;
    }
    .main-doctor__label {
        padding: 8px 16px;
        margin-bottom: 16px;
    }

    .doctor-info {
        &__content {
            padding: 20px;
            gap: 24px;
        }
    
        &__items {
            gap: 12px;
            margin-bottom: 24px;
            & li {
                &:not(:last-child) {
                    padding-bottom: 12px;
                }
            }
            & p {
                font-size: 14px;
            }
        }
    
        &__ratings {
            padding: 16px;
            flex-direction: column;
            gap: 12px;
        }
    
        &__rating {
            flex: 1 0 30%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            &:not(:last-child) {
                padding-right: 0;
                border-right: none;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--temnyy-5);
            }
            &_top {
                margin: 0;
            }
        }
    
        &__photo {
            background-position: top 10% center;
        }
    
        &__img {
            & img {
                max-height: 310px;
                object-position: top;
            }
        }
    
        &__schedule {
            border-radius: 0;
            padding: 0;
            box-shadow: none;
            background: transparent;
            flex-direction: column-reverse;
            gap: 80px;
    
            &_title {
                font-size: 26px;
                margin-bottom: 30px;
            }
    
            & ul {
                gap: 8px;
                & li {
                    flex: 1 0 100%;
                    max-width: 100%;
                    padding: 16px 20px;
                    display: flex;
                    justify-content: space-between;
                    gap: 16px;
                }
            }
        }
    
        &__price {
            font-size: 14px;

        }
    }


    .doctor-education {
        &__list {
            & ul {
                gap: 32px;
                & li {
                    max-width: 100%;
                    padding-top: 0;
                    padding-left: 22px;
    
                    &::after {
                        width: 14px;
                        height: 14px;
                        transform: translate(0, 0);
                        top: 9px;
                    }
                    &::before {
                        height: calc(100% + 5px);
                        width: 2px;
                        top: 29px;
                        left: 6px;
                    }
                }
            }
    
            &_top {
                gap: 12px;
                font-size: 16px;
                margin-bottom: 12px;
    
                & span {
                    padding: 6px 8px;
                }
            }
    
        }
    }


    .doctor-works {
        &__list {
            & ul {
                grid-template-columns: repeat(1, 1fr);
                gap: 12px;
    
                & li a {
                    padding: 12px;
                    gap: 16px;
    
                    &::before {
                        min-width: 47px;
                        width: 47px;
                        height: 47px;
                    }
                }
            }
        }
    }
}