/* service-submenu */
.service-submenu  {
    display: none;
    position: fixed;
    top: 211px;
    left: 0;
    right: 0;
    background: var(--belyy);
    width: 100vw;

    &__row {
        display: flex;
        gap: 60px;
        width: 100%;
    }


    & .header {
        &__submenu {
            &_search {
                border-bottom: 1px solid var(--temnyy-10);
        
                &-form {
                    position: relative;
                    width: 100%;
                    opacity: 0.7;

                    & img {
                        position: absolute;
                        top: 50%;
                        left: 0px;
                        transform: translate(0, -50%);
                    }

                    & input {
                        background: transparent;
                        width: 100%;
                        padding: 32px;
                        padding-left: 40px;
                        height: 100%;
                        font-family: var(--second-family);
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 105%;
                        letter-spacing: -0.01em;
                        color: var(--temnyy-100);
        
                        &::placeholder {
                            font-family: var(--second-family);
                            font-weight: 600;
                            font-size: 20px;
                            line-height: 105%;
                            letter-spacing: -0.01em;
                            color: var(--temnyy-60);
                        }
                    }
                }
            }


            &_tabs {
                width: 100%;
                margin: 0 auto;
            }
    
            &_tab-btns {
                flex: 1 0 36%;
                display: flex;
                flex-direction: column;
                gap: 32px;
                padding: 80px 0;
                padding-right: 60px;
                border-right: 1px solid var(--temnyy-10);
            }
    
            &_tab-btn {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 32px;
                line-height: 105%;
                letter-spacing: -0.01em;
                color: var(--temnyy-100);
                text-align: left;
                opacity: 0.4;
                transition: all 0.3s ease;
                position: relative;
                padding-left: 60px;

                &::before {
                    content: "";
                    display: block;
                    min-width: 23px;
                    width: 23px;
                    height: 20px;
                    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="23" height="20" viewBox="0 0 23 20" fill="none"><path d="M1 10L21 10M21 10L12.1111 19M21 10L12.1111 1" stroke="%23F71729" stroke-width="2" stroke-linecap="round"/></svg>');
                    background-size: contain;
                    background-repeat: no-repeat;
                    position: absolute;
                    top: 8px;
                    left: 0;
                    transition: all 0.2s ease-in;
                }
            
                &:hover {
                    opacity: 1;
                }
                
                &.active {
                    opacity: 1;
                    &::before {
                        transform: translateX(14px);
                    }
                }
            }
    
            &_tab-contents {
                flex: 1 0 55%;

                &.search-active {
                    display: flex;
                    flex-direction: column;
                    gap: 16px;
                    padding: 40px 0;
                    max-height: 510px;
                    overflow-y: auto;
                    padding-right: 16px;
                    min-height: 524px;
                    &::-webkit-scrollbar {
                        width: 3px;
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background: var(--temnyy-20-obvodka-hover-);
                    }

                    & ul {
                        display: contents;
                    }

                }
            }
            &_tab-content {
                display: none;
                width: 100%;
                order: 10;
                padding-top: 80px;
                &.active {
                    display: block;
                }
            
                & ul {
                    display: flex;
                    flex-direction: column;
                    gap: 16px;
                    max-height: 510px;
                    overflow-y: auto;
                    padding-right: 16px;
                    &::-webkit-scrollbar {
                        width: 3px;
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background: var(--temnyy-20-obvodka-hover-);
                    }

                    & a {
                        display: flex;
                        justify-content: space-between;
                        gap: 10px;
                        padding-bottom: 16px;
                        border-bottom: 1px solid var(--temnyy-10);
                        font-family: var(--second-family);
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 105%;
                        letter-spacing: -0.01em;
                        color: var(--temnyy-100);
                        transition: all 0.2s ease-in;
                        opacity: 0.4;

                        &:hover {
                            opacity: 1;
                        }
                    
                        & span {
                            display: block;
                            font-family: var(--font-family);
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 140%;
                            letter-spacing: -0.02em;
                            color: var(--osnovnoy-100);
                        }
                    }
                }
            }
        }
    }
}



@media (max-width: 1460px) {
    .service-submenu  {
        position: static;
        width: 100%;
        margin-top: 10px;
        margin-bottom: 18px;
        transform: translateX(0);

        & .container {
            display: contents;
        }
    
        &__row {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            gap: 10px;
            width: 100%;
            padding-right: 10px;
        }
    
        & .header {
            &__submenu {
                &_search {
                    display: none;
                }
                &_tab-btns {
                    display: contents;
                }
                &_tab-btn {
                    font-size: 16px;
                    padding: 10px 0;
                    width: 100%;
                    opacity: 1;
                    &::before {
                        content: none;
                    }
                    &::after {
                        position: static;
                        border-bottom: 2px solid var(--osnovnoy-100);
                        border-right: 2px solid var(--osnovnoy-100);
                    }
                    &.active::after {
                        transform: rotate(225deg);
                    }
                }
                &_tab-contents {
                    display: contents;
                }
                &_tab-content {
                    width: 100%;
                    padding: 0;
                    gap: 0;
                
                    & ul {
                        height: 100%;
                        max-height: 326px;
                        & a {
                            padding-bottom: 0;
                            border: none;
                            font-weight: 400;
                            font-size: 14px;
                            opacity: 1;
                            color: var(--temnyy-60);
                            & span {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}



