
// text-block
.text-block {
    &__grid {
        display: grid;
        grid-template-columns: 9fr 4fr;
        gap: 60px;
    }


    & h2, & h3, & h4, & h5, & h6 {
        font-family: var(--second-family);
        font-weight: 600;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--temnyy-100);
        margin-bottom: 32px;
    }
    & h2 {
        font-size: 60px;
        &:not(:first-child) {
            margin-top: 90px;
        }
    }
    & h3 {
        font-size: 48px;
        &:not(:first-child) {
            margin-top: 90px;
        }
    }
    & h4 {
        font-size: 32px;
        &:not(:first-child) {
            margin-top: 70px;
        }
    }
    & h5 {
        font-size: 24px;
        &:not(:first-child) {
            margin-top: 50px;
        }
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin-bottom: 0;
        margin-top: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 24px;
        }
    }
    & span {
        //font-weight: 500;
        color: var(--osnovnoy-100);
    }
    & a {
        color: var(--osnovnoy-100);
        text-decoration: underline;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.6;
        }
    }

    & ul {
        padding-top: 16px;
        border-top: 1px solid var(--temnyy-10);
        &:not(:last-child) {
            margin-bottom: 24px;
        }
        & li {
            padding-bottom: 16px;
            border-bottom: 1px solid var(--temnyy-10);
            margin-bottom: 16px;
            position: relative;
            padding-left: 20px;

            &:last-child {
                margin-bottom: 0;
            }

            &::before {
                content: "";
                display: block;
                min-width: 8px;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background-color: var(--osnovnoy-100);
                position: absolute;
                top: 8px;
                left: 0;
            }
        }
    }

    & ol {
        padding-top: 16px;
        border-top: 1px solid var(--temnyy-10);
        list-style-type: none;
        counter-reset: item;
        &:not(:last-child) {
            margin-bottom: 24px;
        }
        & li {
            padding-bottom: 16px;
            border-bottom: 1px solid var(--temnyy-10);
            margin-bottom: 16px;
            position: relative;
            padding-left: 44px;

            &:last-child {
                margin-bottom: 0;
            }

            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 32px;
                width: 32px;
                height: 32px;
                border-radius: 5px;
                background: var(--osnovnoy-100);
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--belyy-100);
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

    & blockquote {
        border: 1px solid var(--temnyy-10);
        border-radius: 10px;
        padding: 40px;
        background: var(--seryy);
        font-style: italic;
        font-weight: 500;
        font-size: 19px;
        line-height: 140%;
        letter-spacing: -0.02em;
        color: var(--temnyy-100);
        position: relative;
        &:not(:last-child) {
            margin-bottom: 24px;
        }

        & p {
            padding-left: 100px;
        }

        &::before {
            content: "";
            display: block;
            width: 56px;
            height: 35px;
            background: url("./../img/icons/quote.svg") no-repeat center / contain;
            position: absolute;
            top: 40px;
            left: 40px;
        }
    }
    & .blockquote {
        &__editor {
            margin: 0 -32px -32px -32px;
            border-radius: 4px;
            padding: 16px 32px;
            box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05);
            background: var(--belyy-100);
            display: flex;
            align-items: center;
            gap: 20px;
            font-style: normal;
            position: relative;
            overflow: hidden;

            &::after {
                content: "";
                display: block;
                width: 163px;
                height: 93px;
                border-radius: 80px / 50px;
                background: var(--osnovnoy-5);
                transform: rotate(-10deg);
                position: absolute;
                bottom: 9px;
                right: -82px;
            }
            &::before {
                content: "";
                display: block;
                width: 133px;
                height: 76px;
                border-radius: 70px / 40px;
                background: var(--osnovnoy-5);
                transform: rotate(-10deg);
                position: absolute;
                bottom: 18px;
                right: -68px;
            }
        
            &_img {
                display: block;
                border-radius: 100px;
                min-width: 80px;
                width: 80px;
                height: 80px;
                overflow: hidden;
                background: var(--seryy);
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        
            &_text {
                & p {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 140%;
                    letter-spacing: -0.02em;
                    color: var(--temnyy-100);
                    padding: 0;
                    margin-bottom: 4px !important;
                }
                & span {
                    font-weight: 400;
                    font-size: 16px;
                    color: var(--temnyy-60);
                }
            }
        }
    }




    &__img {
        & img {
            border-radius: 10px;
            width: 100%;
            max-height: 450px;
            object-fit: cover;
            margin: 10px;
        }
    }


    & table {
        display: block;
        width: 100%;
        border: none;
        border-radius: 10px;
        border: 1px solid var(--seryy);
        overflow: hidden;
        border-collapse: collapse; 
        margin-bottom: 24px;

        & thead {
            & th {
                border: none;
                padding: 20px;
                background: var(--seryy);
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--temnyy-100);
                text-align: left;
                border-right: 1px solid var(--seryy);
            }
            & tr th:last-child {
                border-right: none;
            }
        }

        & tbody {
            & td {
                border-right: 1px solid var(--seryy);
                padding: 20px;
                font-size: 16px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--temnyy-100);
                vertical-align: top;
            }
            & tr td:last-child {
                border-right: none;
            }
            & tr:nth-child(even){
                background: var(--seryy);
            }
        }
    }


    &__literature {
        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 40px;
        }

        & ol {
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding-bottom: 30px;
            border-bottom: 1px solid #c7d6dc;
            margin-bottom: 30px;
            list-style-type: none;
            counter-reset: item;
            & li {
                display: flex;
                gap: 12px;
                color: var(--tegi);
                opacity: 0.85;
                &::before {
                    content: counter(item);
                    counter-increment: item;
                    display: block;
                    min-width: max-content;
                    border: none;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 140%;
                    color: var(--color-temno-zelenyy);
                    opacity: 0.4;
                }
            }
        }

        &_bottom {
            display: flex;
            gap: 20px;
            justify-content: space-between;
        }

        &_item {
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            gap: 15px;
            justify-content: space-between;
            & p {
                margin: 0 !important;
            }
        }

        &_share {
            font-weight: 500;
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 5px;
            background-color: var(--color-zelenyy);
        }
    }



    &__advantages {
        display: flex;
        gap: 24px;
    }
    &__advantage {
        flex: 1 0 45%;
        border-radius: 10px;
        padding: 32px;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.06);
        background: var(--belyy-100);
        & b {
            display: block;
            margin-bottom: 12px;
        }
        & ul {
            & li {
                &:last-child {
                    padding-bottom: 0;
                    border-bottom: none;
                    margin-bottom: 0;
                }
                & p {
                    color: var(--temnyy-60);
                }
            }
        }
    }


    &__right-bottom {
        margin-top: auto;
    }

    &__doctors {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 51px;
    }
    
    &__doctor {
        display: block;
        border: 1px solid var(--temnyy-10);
        border-radius: 10px;
        padding: 32px;
        background: var(--seryy);
        text-decoration: none !important;
        & p:not(:last-child) {
            margin-bottom: 0;
        }

        &:hover {
            opacity: 1 !important;
            & .text-block__doctor_fio {
                color: var(--osnovnoy-100);
            }
        }

        &_top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            font-size: 16px;
            color: var(--temnyy-60);
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(31, 31, 31, 0.06);
            margin-bottom: 16px;
        }
    
        &_main {
            display: flex;
            align-items: center;
            gap: 16px;
            margin-bottom: 16px;
        }

        &_img {
            display: block;
            border-radius: 100px;
            min-width: 88px;
            width: 88px;
            height: 88px;
            overflow: hidden;
            background: var(--belyy-100);
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_fio {
            font-weight: 500;
            font-size: 20px;
            color: var(--temnyy-100);
            transition: all 0.2s ease-in;
            margin-bottom: 4px !important;
        }

        &_post {
            font-size: 16px;
            color: var(--temnyy-60);
            margin-bottom: 20px !important;
        }

        &_date {
            margin: 0 -26px -26px -26px;
            border-radius: 4px;
            padding: 16px 24px;
            box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05);
            background: var(--belyy-100);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            font-size: 16px;
            & p {
                color: var(--temnyy-60);
            }
            & span {
                color: var(--temnyy-100);
            }
        }
    }

    &__lit {
        margin-bottom: 24px;
        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
            margin-bottom: 24px;
        }
    }

    &__estimates {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__estimate {
        border-radius: 10px;
        padding: 12px 24px;
        background: var(--osnovnoy-100);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        font-size: 16px;
        color: var(--belyy-100);

        & p {
            margin: 0 !important;
        }

        &_links {
            display: flex;
            gap: 4px;
        }

        &_link {
            width: 48px;
            height: 48px;
            border-radius: 5px;
            background-color: var(--belyy-20-obvodka-hover);
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.2s ease-in;
            & svg path {
                transition: all 0.2s ease-in;
            }
            &:hover {
                opacity: 1 !important;
                background-color: var(--belyy-100);
                & svg path {
                    fill: var(--osnovnoy-100);
                }
            }
        }
    }
}


.navigation {
    position: sticky;
    top: 110px;
    &__wrap {
        border-radius: 10px;
        padding: 40px;
        background: var(--seryy);
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 32px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--temnyy-100);
        margin-bottom: 32px;
    }

    &__list {
        padding-top: 0 !important;
        border: none !important;
        & > li {
            padding-left: 0 !important;
            padding-right: 50px;
            position: relative;
            &::before {
                content: none !important;
            }
            & > a {

                font-weight: 500;
                font-size: 20px;
                line-height: 140%;
                letter-spacing: -0.02em;
                color: var(--temnyy-100);
                text-decoration: none;
            }
        }
    }

    &__sublist {
        border: none !important;
        display: none;
        &.open {
            display: block;
        }
        & > li {
            padding-bottom: 0 !important;
            border: none !important;
            & > a {
                color: var(--temnyy-60);
                text-decoration: none;
            }
        }
    }
    
    & .toggle-sublist {
        background: var(--osnovnoy-100);
        border-radius: 5px;
        min-width: 32px;
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        right: 0;
    
        color: white;
        cursor: pointer;
        float: right;
    
        &::after {
            content: "";
            display: inline-block;
            width: 8px;
            height: 8px;
            border-bottom: 2px solid var(--belyy-100);
            border-right: 2px solid var(--belyy-100);
            transform: rotate(45deg);
            transition: all 0.2s ease;
        }
    
        &.open::after {
            transform: rotateZ(225deg);
        }
    }
}


.help {
    border-radius: 10px;
    padding: 60px;
    background: var(--osnovnoy-100);
    color: var(--belyy-100);
    margin: 60px 0;
    position: relative;
    overflow: hidden;

    &::after, &::before {
        content: "";
        display: block;
        width: 700px;
        height: 400px;
        border-radius: 350px / 200px;
        background: var(--belyy-10);
        transform: rotate(-15deg);
        position: absolute;
        bottom: -290px;
        right: -380px;
    }
    &::after {
        bottom: -200px;
        right: -360px;
    }
    &::before {
        bottom: -220px;
        right: -380px;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 48px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--belyy-100);
        margin-bottom: 28px;
    }
    &__items ul {
        display: flex;
        flex-wrap: wrap;
        gap: 12px;
        padding: 0;
        border: none;
        margin-bottom: 48px;

        & li {
            flex: 1 0 45%;
            border: 1px solid var(--belyy-20-obvodka-hover);
            border-radius: 10px;
            padding: 20px;
            background: var(--belyy-10);
            margin: 0;
            position: relative;
            padding-left: 80px;

            &::before {
                content: "";
                display: block;
                width: 40px;
                height: 22px;
                background: url("./../img/icons/list-item-white.svg") no-repeat center / contain;
                position: absolute;
                top: 20px;
                left: 20px;
            }
        }
    }
    &__warning {
        font-weight: 500;
        font-size: 24px;
        padding-bottom: 16px;
        border-bottom: 1px solid var(--belyy-20-obvodka-hover);
        margin-bottom: 16px;
        display: flex;
        align-items: center;
        gap: 16px;
        &::before {
            content: "!";
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 48px;
            width: 48px;
            height: 48px;
            border-radius: 10px;
            background: var(--belyy-100);
            color: var(--osnovnoy-100);
        }
    }
    & button {
        width: 100%;
    }
    &__btn {
        margin: 0 !important;
    }
}


// media text-block
@media (max-width: 1100px) {
    .text-block {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 40px;
        }
        &__right {
            display: contents;
        }
        &__img {
            margin-bottom: 20px;
            & img {
                max-height: 400px;
            }
        }
        &__doctors, &__estimates {
            flex-direction: row;
        }
        &__doctor, &__estimate {
            flex: 1 0 40%;
        }
        &__advantages {
            flex-direction: column;
        }
    }
    .navigation {
        position: static;
        order: -1;
    }
}
@media (max-width: 768px) {
    .text-block {
        & h2, & h3, & h4, & h5, & h6 {
            margin-bottom: 20px;
        }
        & h2 {
            font-size: 26px;
            &:not(:first-child) {
                margin-top: 50px;
            }
        }
        & h3 {
            font-size: 24px;
            &:not(:first-child) {
                margin-top: 50px;
            }
        }
        & h4 {
            font-size: 20px;
            &:not(:first-child) {
                margin-top: 40px;
            }
        }
        & h5 {
            font-size: 18px;
            &:not(:first-child) {
                margin-top: 40px;
            }
        }
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
        & ul {
            padding-top: 14px;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
            & li {
                padding-bottom: 14px;
                margin-bottom: 14px;
            }
        }
        & ol {
            padding-top: 14px;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
            & li {
                padding-bottom: 14px;
                margin-bottom: 14px;
                padding-left: 38px;

                &::before {
                    min-width: 26px;
                    width: 26px;
                    height: 26px;
                    font-size: 14px;
                }
            }
        }

        & blockquote {
            padding: 20px;
            font-size: 16px;
    
            & p {
                padding-left: 0;
                padding-top: 35px;
            }
    
            &::before {
                width: 30px;
                height: 19px;
                top: 20px;
                left: 20px;
            }
        }
        & .blockquote {
            &__editor {
                margin: 0 -14px -14px -14px;
                padding: 14px;
                gap: 12px;
    
                &::after {
                    bottom: 6px;
                    right: -119px;
                }
                &::before {
                    bottom: 14px;
                    right: -107px;
                }
            
                &_img {
                    min-width: 60px;
                    width: 60px;
                    height: 60px;
                }
            
                &_text {
                    & p {
                        font-size: 16px;
                    }
                    & span {
                        font-size: 14px;
                    }
                }
            }
        }

        &__img {
            & img {
                max-height: 230px;
            }
        }
        & table {
            overflow-x: auto;
            max-width: 100%;
            max-width: calc(100vw - 20px);
            & thead th, & tbody td {
                min-width: 243px;
            }
            &::-webkit-scrollbar {
                height: 3px;
                padding-top: 10px;
            }
            &::-webkit-scrollbar-thumb {
                background: var(--osnovnoy-100);
            }
        }


        &__doctors {
            flex-direction: column;
            gap: 10px;
            margin-bottom: 40px;
        }
        
        &__doctor {
            padding: 20px;
            & p:not(:last-child) {
                margin-bottom: 0;
            }

            &_top {
                font-size: 14px;
                padding-bottom: 12px;
                margin-bottom: 12px;
                & img {
                    display: none;
                }
            }
            &_main {
                gap: 12px;
            }
            &_img {
                min-width: 60px;
                width: 60px;
                height: 60px;
            }
    
            &_fio {
                font-size: 16px;
            }
            &_post {
                font-size: 14px;
                margin-bottom: 12px !important;
            }
            &_date {
                margin: 0 -12px -12px -12px;
                padding: 12px 16px;
                font-size: 14px;
            }
        }
    
        &__lit {
            margin-bottom: 20px;
            &_title {
                font-size: 24px;
                margin-bottom: 20px;
            }
        }
    
        &__estimates {
            flex-direction: column;
            gap: 8px;
        }
    
        &__estimate {
            padding: 12px 12px 12px 16px;
            font-size: 14px;
            &_link {
                min-width: 40px;
                width: 40px;
                height: 40px;
            }
        }



        &__advantages {
            gap: 20px;
        }
        &__advantage {
            flex: 1 0 45%;
            border-radius: 0;
            padding: 0;
            box-shadow: none;
            background: transparent;
            & b {
                margin-bottom: 10px;
            }
            & ul {
                & li {
                    &:last-child {
                        padding-bottom: 14px;
                        border-bottom: 1px solid var(--temnyy-10);
                    }
                }
            }
        }
    }

    .navigation {
        &__wrap {
            padding: 20px;
        }
        &__title {
            font-size: 20px;
            margin-bottom: 24px;
        }
        &__list {
            & > li {
                padding-right: 40px;
                & > a {
                    font-size: 16px;
                }
            }
        }
        & .toggle-sublist {
            min-width: 26px;
            width: 26px;
            height: 26px;
            &::after {
                width: 6px;
                height: 6px;
                border-bottom: 2px solid var(--belyy-100);
                border-right: 2px solid var(--belyy-100);
            }
        }
    }

    .help {
        padding: 40px 10px 80px 10px;
        margin: 80px -10px;
        border-radius: 0;
        &::after {
            bottom: -240px;
            right: -300px;
        }
        &::before {
