// site-map
.site-map {
    &__grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__accordion {
        display: contents;
    }
    &__item {
        border-radius: 10px;
        padding: 32px;
        background: var(--seryy);

        &_top {
            margin-bottom: 24px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 105%;
            letter-spacing: -0.01em;
            color: var(--temnyy-100);
        }


        &_list {
            &.accordion__content {
                display: contents;
            }
            & ul {
                display: flex;
                flex-direction: column;
                gap: 16px;
                & li {
                    &:not(:last-child) {
                        padding-bottom: 16px;
                        border-bottom: 1px solid var(--temnyy-10);
                    }
                    & a {
                        display: flex;
                        gap: 10px;
                        font-size: 16px;
                        color: var(--temnyy-100);
                        transition: all 0.3s ease;
                        &:hover {
                            color: rgba(31, 31, 31, 0.8);
                        }
                        &::before {
                            content: "";
                            display: block;
                            min-width: 16px;
                            width: 16px;
                            height: 14px;
                            background-image: url("data:image/svg+xml;utf8,<svg width='16' height='14' viewBox='0 0 16 14' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1 7L14 7M14 7L8.22222 13M14 7L8.22222 1' stroke='%23F71729' stroke-width='1.5' stroke-linecap='round'/></svg>");
                            background-repeat: no-repeat;
                            margin-top: 7px;
                        }
                    }
                }
            }
        }
    }
}

// media site-map
@media (max-width: 1370px) {
    .site-map {
        &__grid {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 1050px) {
    .site-map {
        &__grid {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 700px) {
    .site-map {
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
        &__accordion {
            display: block;
            &.accordion--active {
                margin-bottom: 24px;
                & .site-map__item_title::after {
                    transform: rotate(225deg);
                    margin: 0;
                }
            }
        }
        &__item {
            border-radius: 10px;
            padding: 20px;
            &_top {
                padding-bottom: 0;
                border-bottom: none;
                margin-bottom: 0;
            }
            &_list {
                &.accordion__content {
                    display: block;
                }
                & ul li a {
                    &::before {
                        min-width: 12px;
                        width: 12px;
                        height: 11px;
                        background-size: contain;
                        margin-top: 5px;
                    }
                }
            }
            &_title {
                font-size: 18px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 20px;
                &::after {
                    content: "";
                    display: inline-block;
                    min-width: 8px;
                    width: 8px;
                    height: 8px;
                    border-bottom: 2px solid var(--osnovnoy-100);
                    border-right: 2px solid var(--osnovnoy-100);
                    transform: rotate(45deg);
                    transition: all 0.3s ease;
                }
            }
        }
    }
}

