
// symptoms
.symptoms {
    padding: 160px 0 170px;
    background: var(--osnovnoy-100);
    position: relative;
    overflow: hidden;

    &__elipse {
        content: "";
        display: block;
        width: 912px;
        height: 520px;
        border-radius: 400px/250px;
        background:var(--belyy-10);
        transform: rotate(-15deg);
        position: absolute;
    }
    &__elipses-top {
        & span {
            &:nth-child(1) {
                top: -100%;
                top: -440px;
                left: -55px;
            }
            &.animation-active:nth-child(1) {
                top: -324px;
            }
        }
        & span:nth-child(2) {
            top: -420px;
            left: -55px;
        }
    }
    &__elipses-bottom {
        & span {
            width: 1166px;
            height: 609px;
            border-radius: 560px/300px;

            &:nth-child(1) {
                bottom: -530px;
                right: -96px;
            }
            &:nth-child(2) {
                bottom: -560px;
                right: -96px;
            }
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__content {
        max-width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        position: relative;
        z-index: 2;
    }

    &__title {
        color: var(--belyy-100);
        margin-bottom: 20px;
    }

    &__bottom {
        max-width: 415px;
        color: var(--belyy-100);
    }

    &__texts {
        padding-bottom: 24px;
        border-bottom: 1px solid var(--belyy-20-obvodka-hover);
        margin-bottom: 24px;
    }


    &__text {
        margin-bottom: 20px;
    }

    &__phone {
        border: 1px solid var(--belyy-100);
        border-radius: 10px;
        padding: 16px 20px;
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 20px;
        line-height: 105%;
        letter-spacing: -0.01em;
        color: var(--belyy-100);
        display: flex;
        align-items: center;
        gap: 8px;
        width: fit-content;
        color: var(--osnovnoy-100);
        background: var(--belyy-100);
        transition: all 0.2s ease-in;
        
        & svg path {
            transition: all 0.2s ease-in;
        }

        &:hover {
            background: var(--osnovnoy-100);
            color: var(--belyy-100);
            & svg path {
                fill: var(--belyy-100);
            }
        }
    }

    &__btn {
        width: 100%;
    }

    &__list {
        max-width: 50%;
        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
            & li {
                flex: 1 0 45%;
                border: 1px solid var(--belyy-20-obvodka-hover);
                border-radius: 10px;
                padding: 32px;
                background: var(--belyy-10);
                color: var(--belyy-100);
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                gap: 20px;
                min-height: 219px;

                &::before {
                    content: "";
                    display: block;
                    width: 40px;
                    height: 22px;
                    background-image: url("./../img/icons/list-item-white.svg");
                    background-size: contain;
                    background-repeat: no-repeat;
                }
            }
        }
    }
}

// media symptoms
@media (max-width: 1080px) {
    .symptoms {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
    
        &__content {
            display: contents;
        }
        &__bottom {
            order: 3;
            max-width: 100%;
        }
    
        &__list {
            order: 2;
            max-width: 100%;
        }
    }
}

@media (max-width: 768px) {
    .symptoms {
        padding: 80px 0;

        &__elipses-top {
            & span {
                &:nth-child(1) {
                    left: -150px;
                }
            }
            & span:nth-child(2) {
                left: -150px;
            }
        }
        &__elipses-bottom {
            & span {
                width: 1166px;
                height: 609px;
                border-radius: 560px/300px;
    
                &:nth-child(1) {
                    bottom: -400px;
                    right: -500px;
                }
                &:nth-child(2) {
                    bottom: -430px;
                    right: -500px;
                }
            }
        }

        &__row {
            gap: 0;
        }

        &__title {
            color: var(--belyy-100);
            margin-bottom: 24px;
        }

        &__text {
            margin-bottom: 16px;
        }
    
        &__phone {
            font-size: 16px;
            justify-content: center;
            width: 100%;
        }
    
        &__list {
            margin-bottom: 40px;
            & ul {
                gap: 12px;
                & li {
                    flex: 1 0 100%;
                    padding: 14px;
                    flex-direction: row;
                    justify-content: start;
                    gap: 16px;
                    min-height: auto;
                    &::before {
                        width: 32px;
                        height: 18px;
                    }
                }
            }
        }
    }
}