// quickly
.quickly {
    position: relative;

    &__wrapper {
        padding: 60px 0;
        display: flex;
        justify-content: space-between;
        align-items: start;
    }

    &__contant {
        border-radius: 10px;
        padding: 60px;
        width: 60%;
        max-width: 850px;
        min-height: 680px;
        box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.08);
        background: var(--belyy-100);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        position: relative;
        z-index: 3;
    }

    &__selects {
        display: flex;
        gap: 20px;
        padding-bottom: 32px;
        border-bottom: 1px solid var(--temnyy-10);
        margin-bottom: 32px;
    }

    &__select {
        flex: 1 0 45%;
    }

    &__results {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__result {
        flex: 1 0 48%;
        display: flex;
        align-items: center;

        &_time {
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            letter-spacing: -0.02em;
            color: var(--osnovnoy-100);
            text-align: center;
            
            & span {
                font-size: 48px;
                line-height: 90%;
            }
        }
        
        &_text {
            font-size: 16px;
            padding-left: 16px;
            border-left: 1px solid var(--temnyy-10);
            margin-left: 16px;
        }
    }

    &__btn {
        flex: 1 0 48%;
    }


    &__maps {
        width: 100%;
        height: 100%;
        min-height: 666px;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        border-radius: 10px;
        overflow: hidden;
        max-width: 70%;
        background-color: #fafafa;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }

    &__qnt {
        position: relative;
        z-index: 3;
        border-radius: 10px;
        padding: 16px 20px;
        box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.08);
        background: var(--belyy-100);
        display: flex;
        align-items: center;
        gap: 12px;
        font-size: 16px;
        line-height: 130%;
        color: var(--temnyy-60);

        & span {
            font-weight: 500;
            font-size: 44px;
            line-height: 105%;
            color: var(--osnovnoy-100);
        }
    }
}


// media quickly
@media (max-width: 1100px) {
    .quickly {
        &__container {
            display: flex;
            flex-direction: column;
        }
    
        &__wrapper {
            display: contents;
        }
    
        &__contant {
            display: contents;
        }

        &__title {
            order: 0;
            margin-bottom: 30px;
        }

        &__inner {
            order: 3;
        }

        &__btn p {
            width: 100%;
        }
    
        &__maps {
            order: 1;
            min-height: 600px;
            position: relative;
            max-width: 100%;
            margin-bottom: 16px;
        }

        &__qnt {
            order: 2;
            border-radius: 0;
            padding: 0;
            box-shadow: none;
            background: transparent;
            flex-direction: row-reverse;
            justify-content: space-between;
            padding-bottom: 24px;
            border-bottom: 1px solid var(--temnyy-10);
            margin-bottom: 24px;
        }
    }
}
@media (max-width: 768px) {
    .quickly {
        &__selects {
            flex-direction: column;
            gap: 16px;
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 24px;
        }

        &__results {
            flex-direction: column;
            align-items: stretch;
        }
    
        &__result {
            &_time {
                font-size: 16px;
                & span {
                    font-size: 40px;
                }
            }
            
            &_text {
                padding-left: 15px;
                margin-left: 15px;
            }
        }
    
        &__btn {
            flex: 1 0 48%;
        }
    
    
        &__maps {
            min-height: 350px;
        }
    
        &__qnt {
            & span {
                font-size: 34px;
            }
        }
    }
}